.tools-collections {
  position: absolute;
  min-width: 254px;
  max-width: 275px;
  top: 36px;
  right: 0;
  padding: 8px;
  border-radius: 5px;
  background-color: #fff;
  z-index: 10;
  opacity: 0;
  visibility: hidden;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  transition: opacity .15s;
  &-new {
    box-sizing: border-box;
    position: relative;
    width: 100%;
    height: 40px;
    padding: 0 40px 0 15px;
    border-radius: 7px;
    background-color: #fff;
    border: solid 1px #dcdcdc;
    overflow: hidden;
  }
  &-input {
    width: 100%;
    height: 40px;
    background: transparent;
    font-size: 16px;
    line-height: 40px;
    border: 0;
    outline-color: #1abc9c;
  }
  &-add {
    position: absolute;
    top: 0;
    right: 0;
    width: 40px;
    height: 40px;
    background-color: transparent;
    border: 0 none;
    cursor: pointer;
    &:before,
    &:after {
      position: absolute;
      content: '';
      background-color: #0f9d58;
    }
    &:before {
      top: 17px;
      left: 12px;
      width: 17px;
      height: 3px;
    }
    &:after {
      top: 10px;
      left: 19px;
      width: 3px;
      height: 17px;
    }
  }
  &-list {
    list-style: none;
  }
  &-item {
    box-sizing: border-box;
    position: relative;
    display: block;
    width: 100%;
    height: 40px;
    margin-top: 5px;
    padding: 0 40px 0 15px;
    background-color: #f3f3f3;
    line-height: 40px;
    white-space: nowrap;
    border-radius: 8px;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
    &:before,
    &:after {
      position: absolute;
      content: '';
      background-color: #0f9d58;
    }
    &:before {
      top: 18px;
      right: 12px;
      width: 17px;
      height: 3px;
    }
    &:after {
      top: 11px;
      right: 19px;
      width: 3px;
      height: 17px;
    }
    &:hover {
      text-decoration: underline;
    }
    &.is-added:after {
      display: none;
    }
  }
}
