@media only screen and (max-width: 1150px) {
	.main {
		.header {
			.wrapper {
				padding-bottom: 50px;
			}
			&-title {
				font-size: 48px;
				span {
					display: inline;
				}
			}
		}
	    .info {
			&-title {
				left: 7%;
				right: 7%;
				top: 20px;
				font-size: 18px;
			}
			&-text {
				bottom: 90px;
				font-size: 16px;
				text-shadow: 0 0 0 #fff;
				margin: 0;
			}
			&-button {
				position: absolute;
				bottom: 20px;
				left: 7%;
				right: 7%;
			}
		}
		.nav {
			&-link {
				.icon {
					&::before {
						display: block;
						font-size: 40px;
					}
					span {
						padding: 0 4px;
					}
				}
				&:hover .icon span {
					font-size: 12px;
				}
			}
		}
		.capabilities {
			&-block {
				&-1 {
					top: 50px;
				}
				&-3 {
					top: 50px;
				}
				&-4 {
					min-height: 150px;
				}
			}
			&-title {
				font-size: 18px;
			}
			&-text {
				font-size: 14px;
			}
		}
		.goods-tools {
			width: 42%;
		}
		.tools-dropbox-button {
			min-width: 110px;
			.user-tools & {
				max-width: 200px;
				white-space: nowrap;
			}
		}
	}
	.shop-account,
	.shop {
		.brand-item {
			.brand {
				&-goods-item,
				&-goods-more {
					width: 65px;
					height: 65px;
					font-size: 14px;
				}
				&-image {
					width: 80px;
					height: 80px;
					left: -100px;
				}
				&-name {
					font-size: 20px;
				}
				&-author {
					font-size: 16px;
				}
			}
			&-info {
				&-name {
					font-size: 20px;
					margin-bottom: 5px;
				}
				&-text {
					font-size: 12px;
				}
			}
		}
		&-creation {
			.step {
				.field-body {
					.country {
						width: 240px;
					}
					.city {
						width: 300px;
					}
				}
			}
		}
	}
	.collection {
		&-name {
			font-size: 20px;
		}
		&-info {
			font-size: 14px;
			small {
				font-size: 14px;
				margin: 0 0 10px;
			}
		}
	}
	.goods-item .tile-list-item-type,
	.goods-item .tile-list-item-state,
	.collection-item .tile-list-item-state {
		font-size: 18px;
	}
	.collection-item .tile-list-item-info,
	.goods-item .tile-list-item-info {
		margin-bottom: 0;
	}
	.button-compilation {
		padding: 0 1em;
	}
	.goods-item .tile-list-item-type .button-action {
	    margin-top: 78%;
	}
	.pseudo-popup .step .brand-url + .field-input-text {
	    width: 694px;
	}
	.popup-add-collection .button {
	    min-width: 220px;
	}
	.goods-item .tile-list-item-type {
		padding-top: 0;
	}
	.shop-account .goods-item .tile-list-item-state,
	.shop-account .goods-item .tile-list-item-type {
		.tile-list-item-info {
			position: absolute;
			height: auto;
			overflow: hidden;
			text-overflow: ellipsis;
			top: 0;
			left: 0;
			right: 0;
			margin-top: 55%;
			transform: translateY(-50%);
			padding-left: 5px;
			padding-right: 5px;
			max-height: 94px;
			margin-top: 48%;
			line-height: 1.2;
		}
		.button {
			position: absolute;
			left: 50%;
			top: 0;
			margin-top: 76%;
			transform: translateX(-50%);
		}
	}
	.footer {
		font-size: 15px;
		&-menu {
			margin: 0 0 15px;
		}
	}
	.content-toolbar {
		height: 63px;
		&-part {
			padding: 15px 15px 15px 0;
			&:first-child {
				margin-left: 15px;
			}
		}
		&-search {
			&-text {
				height: 33px;
				padding: 0 30px 0 10px;
				font-size: 15px;
			}
			&-button {
				font-size: 20px;
				width: 21px;
				top: 5px;
				right: 7px;
			}
		}
		&-sort .select2-container--default .select2-selection--single {
			height: 34px;
			font-size: 15px;
			line-height: 34px;
			.select2-selection__rendered {
			    line-height: 34px;
			    padding: 0 30px 0 10px;
			}
			.select2-selection__arrow {
				top: 13px;
				right: 10px;
				b {
					border-width: 7px 7px 0;
				}
			}
		}
		&-range {
			padding: 10px 30px 0 20px;
		}
		&.button-toolbar {
			height: 80px;
		}
	}
	div.tree-multiselect {
		font-size: 14px;
		&-dropbox {
			right: 15px;
		}
		&-button {
			height: 34px;
			font-size: 15px;
			line-height: 34px;
			padding: 0 30px 0 10px;
			&::after {
				border-width: 7px 7px 0;
				top: 13px;
				right: 10px;
			}
		}
		div.title {
			padding-right: 35px;
			> span.collapse-section {
				border-width: 7px 7px 0;
			}
		}
	}
	.subscriber {
		&-items {
			&-content {
				margin-top: -.6em;
			}
			&-unit {
				display: none;
			}
		}
	}


	.shop {
		&-account {
			&-navigation li {
				margin: 0 20px;
				a {
			    font-size: 16px;
				}
			}
			.drag-n-drop {
			    height: 380px;
			}
			.add-tool span {
				font-size: 13px;
			}
		}

		&-creation-nav {
			& .button {
				padding: 0 1em;
			}

			&-settings .brand-remove {
				max-width: 30%;
			}
		}

	}

	.brand-settings-history {

		&-button {
			padding: 0 20px;
		}
	}

}
