.cart {

  .content-toolbar {
    margin-bottom: 8px;
  }

  .content-toolbar-part:first-child {
    margin-left: 0;
  }

  .content-toolbar-tabs a:not(:first-of-type) {
    margin-left: 72px;
  }

  .carousel.not-available {
    .carousel-item {
      position: relative;
    }
    .carousel-title:hover {
      .carousel-title-text {
        text-decoration: none;
        span {
          text-decoration: underline;
        }
      }
    }
    .carousel-title-text {
      display: inline-block;
      width: 65%;
      height: auto;
      padding-top: 24px;
      line-height: 20px;
      p {
        margin: 16px 0 0 -50px;
        color: $textColor;
        font-size: 16px;
        font-weight: 400;
        line-height: 16px;
      }
    }
    .carousel-title:before {
      top: -19px;
    }
    .cart-quick-info {
      position: absolute;
      top: 0;
      right: 34px;
      width: 280px;
      @media #{$desktop} {
        width: 215px;
      }
    }
    td.cart-details-summary:first-child {
      color: #ff0000;
    }
    .cart-details-item {
      margin-bottom: 30px;
    }
    .cart-quick-info .button {
      background: #ffc107;
    }
  }

  .carousel-title {
    width: calc(100% - 118px);
    height: 102px;
    padding-left: 84px;
    padding-right: 34px;
    color: $txt-black;
    line-height: 102px;

    &:before {
      color: $linkColor;
    }
  }

  .carousel-title-text {
    width: 40%;
    float: left;
    display: flex;
    align-items: center;
    height: 102px;
    line-height: 20px;
  }

  .carousel-item-content {
    margin-top: auto;
    padding-top: 20px;
  }

  &-quick-info {
    width: 60%;
    float: right;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 102px;

    small {
      font-size: 16px;
      color: $textColor;
      line-height: 16px;
      text-align: right;
    }
    p {
      padding: 0 20px 0 10px; 
      color: $linkColor;
      font-size: 35px;
      font-weight: 400;
      white-space: nowrap;

      span {
        font-weight: 900;
      }
    }
    @media #{$desktop} {
      small {
        font-size: 14px;
      }

      p {
        font-size: 28px;
      }

      .button {
        padding: 0 20px; 
      }
    }
  }

  hr {
    top: 102px;
  }

  &-details {
    display: flex;
    flex-direction: column;
    padding: 35px 34px 0;
    
    &-item {
      display: flex;

      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }

    &-img {
      width: 250px;
      min-width: 250px;
      height: 250px;
      background-size: cover;
      background-position: 50%; 
    }

    &-content {
      position: relative;
      width: 80%;
      padding: 20px 30px;
      background-color: #fff;
    }

    &-close {
      position: absolute;
      top: 10px;
      right: 10px;
      width: 13px;
      height: 13px;
      padding: 10px;
      font-size: 13px;
      color: #bf1b39;

      &:hover {
        color: #bf1b39;
      }
    }

    &-title {
      font-size: 22px;
      font-weight: 500;
    }

    &-options {
      width: 80%;
      margin-top: 20px;

      @media only screen and (max-width: 1150px) {
        width: 100%;
      }

      th,
      td {
        width: 33%;
        text-align: left;
      }

      th {
        padding-bottom: 10px;
        font-size: 20px;
        font-weight: 500;
      }

      tbody {

        tr:nth-child(2) {
          td {
            padding-top: 20px;
            font-size: 20px;
            font-weight: 500;
            vertical-align: top;

            span {
              font-weight: 400;
            }
          }

          td:nth-child(3) {
            span {
              color: $linkColor;
              font-weight: 500;
            }
          }
        }        
      }
    }
  }

  &-comment-link {
    display: block;
    padding: 17px 0;
    font-size: 17px;
    text-align: center;
  }

  &-agreement {
    margin-top: 30px;
    font-size: 16px;
    line-height: 1.38;
    text-align: right;
  }

  .shop-creation-nav {
    margin-top: 30px;
  }
}
