.discussion {
	&-list {
		margin: 0;
		padding: 0;
		list-style: none;
	}
	&-item {
		margin-top: 50px;
		padding: 50px 0 0 140px;
		border-top: 2px solid #dedede;
		&:first-child {
			padding-top: 0;
			margin-top: 0;
			border: 0;
		}
	}
	&-body {
		position: relative;
		font-weight: 400;
		.discussion-author {
			float: left;
			margin-top: 10px;
			padding-top: 0;
		}
	}
	&-image {
		position: absolute;
		left: -140px;
		top: 0;
		width: 100px;
		height: 100px;
	}
	&-author {
		padding-top: 10px;
		margin-right: 25px;
		font-size: 25px;
		font-weight: 500;
        word-break: break-word;
		a {
			color: inherit;
		}
	}
	&-topic {
		display: block;
		margin-left: -15px;
		padding: 40px 15px 12px;
		color: inherit;
		&:hover {
			text-decoration: none;
			background: #f3f3f3;
			color: inherit;
		}
	}
	&-date {
		margin-bottom: 10px;
		font-size: 14px;
		line-height: 1.4;
		color: #7f8c8d;
	}
	&-counter {
		float: right;
		font-size: 14px;
		color: #7f8c8d;
	}
	&-text {
		color: #383838;
	}
	&-remove {
		position: absolute;
		right: 15px;
		top: 15px;
		&-link.icon {
			padding: 1px;
			display: block;
			font-size: 16px;
			height: 18px;
			width: 18px;
			letter-spacing: 2px;
			padding-left: 1px;
			box-sizing: border-box;
			overflow: hidden;
			color: #bf1b39;
		}
		&-reason,
		&-done {
			display: none;
			position: relative;
			min-height: 100px;
			& .discussion-image {
				opacity: .3;
			}
			& .discussion-author,
			& .discussion-date,
			& .discussion-text {
				color: #ccc;
			}
			a {
				font-weight: 400;
			}
		}
		&-reason {
			form {
				margin: 0;
			}
			b {
				display: block;
				margin-top: 15px;
			}
			label {
				display: block;
				margin: -10px 0 10px;
				&:first-child {
					margin-top: 10px;
				}
			}
		}
		&-done {
			color: #ccc;
		}
	}
}
