/* Slider */

.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}
.slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0;

    &:focus {
        outline: none;
    }

    &.dragging {
        cursor: pointer;
        cursor: hand;
    }

    .js-guys & {
      overflow: visible;
    }
}
.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: block;

    &:before,
    &:after {
        content: "";
        display: table;
    }

    &:after {
        clear: both;
    }

    .slick-loading & {
        visibility: hidden;
    }
}
.slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;
    [dir="rtl"] & {
        float: right;
    }
    img {
        display: block;
    }
    &.slick-loading img {
        display: none;
    }

    display: none;

    &.dragging img {
        pointer-events: none;
    }

    .slick-initialized & {
        display: block;
    }

    .slick-loading & {
        visibility: hidden;
    }

    .slick-vertical & {
        display: block;
        height: auto;
        border: 1px solid transparent;
    }

    &:focus {
        outline: none;
    }
}
.slick-arrow.slick-hidden {
    display: none;
}


.slick {
    &-arrow {
        position: absolute;
        display: block;
        border: none;
        font-size: 0;
        height: 38px;
        width: 20px;
        top: 50%;
        margin-top: -19px;
        background: url(../img/ic_next_white.svg) 0 0 no-repeat;
        cursor: pointer;
        z-index: 10;
        &:hover {
            opacity: 0.8;
        }
        &.slick-disabled {
            opacity: 0.2;
        }
    }
    &-slider.review-slider,
    &-slider.product-recently-slider {
        .slick-arrow {
            background: url(../img/ic_next_black.svg) 0 0 no-repeat;
        }
    }
    &-next {
        right: 0;
        margin-right: 30px;
    }
    &-prev {
        left: 0;
        margin-left: 30px;
        -moz-transform: scaleX(-1);
        -o-transform: scaleX(-1);
        -webkit-transform: scaleX(-1);
        transform: scaleX(-1);
        filter: FlipH;
        -ms-filter: "FlipH";
    }
    &-slider {
        &.review-slider,
        &.reviews-list {
            .slick-next {
                margin-right: -40px;
            }
            .slick-prev {
                margin-left: -40px;
            }
        }
    }

}
