.page-menu {
	margin: 0;
	padding: 30px 0;
	text-align: center;
	list-style: none;
	background: #f3f3f3;
	&-item {
		display: inline-block;
	}
	&-link {
		font-size: 20px;
		font-weight: 500;
		color: #383838;
		text-decoration: none;
		text-align: center;
		position: relative;
		display: block;
		padding: 45px 0 0;
		margin: 0 25px;
		&:hover,
		&.active {
			color: #ff5722;
			text-decoration: none;
		}
		&::before {
			position: absolute;
			font-size: 38px;
			top: 0;
			left: 50%;
			transform: translate(-50%, 0);
		}
		&.icon-shop-on::before,
		&.icon-shop-off::before,
		&.icon-graduate-off::before {
			font-size: 50px;
			top: -5px;
		}
		small {
			position: absolute;
			font-size: 27px;
			font-weight: 400;
			top: 5px;
			left: 50%;
			transform: translate(-50%, 0);
		}

		&.icon-heart.active::before,
		&.icon-heart:hover::before {
			content: "u";
		}
		&.icon-community.active::before,
		&.icon-community:hover::before {
			content: "*";
		}
		&.icon-make-off.active::before,
		&.icon-make-off:hover::before {
			content: "v";
		}
		&.icon-shop-off.active::before,
		&.icon-shop-off:hover::before {
			content: ">";
		}
		&.icon-graduate-off.active::before,
		&.icon-graduate-off:hover::before {
			content: "^";
		}
	}

	&.no-icons &-link { padding-top: 0; }
}