/* Communities */
.communities {
    .header {
        background-size: cover;
        background-position: 50% 50%;
        color: #fff;
        height: $page-header-height;
        box-sizing: border-box;
        display: flex;
        align-items: flex-end;
        position: relative;

        .wrapper {
            width: 100%;
            padding: 50px 70px;
            display: flex;
            align-items: flex-end;
            justify-content: space-between;
        }

        &-title {
            margin: 0;
            font-size: 30px;
            line-height: 1.2;
            font-weight: 400;
        }

        .button {
            padding: 0 16px;
            height: 55px;
            line-height: 54px;
            white-space: nowrap;
            background: #ffc107;
            
            &:hover {
                background: darken(#ffc107, 5%);
            }
        }
    }
}