.collection {
	&-item {
		&:not(.collection-item-brand)::before {
			content: '';
			@include posa(50%, 0, 0, 0);
			background: rgba(0, 0, 0, .5);
			transition: background .15s;
			z-index: 1; 
		}
		&:not(.collection-item-change):hover {
			&::before {
				background: #ff5722;
			}
			& .collection-info {
				max-height: 12em;
				overflow: hidden;
			}
		}
		&.tile-list-item-removed {
			&::before,
			.collection-link {
				display: none;
			}
			.collection-image::before {
				content: '';
				@include posa(0, 0, 0, 0);
				background: rgba(168, 0, 0, .7);
        opacity: 1;
			}
		}
		.tile-list-item {
			&-state {
				padding-top: 30%;
        z-index: 10;
			}
			&-info {
				margin-bottom: 2em;
			}
		}

		.tile-list-item-type {
			position: absolute;
      margin-top: 60px;
			right: 0;
			left: 50%;
			z-index: 10;
      @media only screen and (max-width: 1150px) {
        margin-top: 35px;
      }
		}

		&-brand {

			.brand-card {
				left: 27px;
				bottom: 27px;
			}

			.brand-image {
				width: 100px;
				height: 100px;
				left: 0;
			}

			.brand-name {
				font-size: 25px;
				font-weight: 500;
			}

			.collection-link:hover,
			.collection-link {
				padding-left: 0;
			}

			.tile-list-item-type {
			    position: absolute;
			    top: 50%;
			    left: 50%;
			    bottom: auto;
			    transform: translate(-50%, -50%);
			}

			&::before {
				content: "";
				display: block;
				position: absolute;
				height: 130px;
				left: 0;
				right: 0;
				bottom: 0;
				background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.25));
			}

		}
	}

	&-image {
		display: block;
		position: relative;
		width: 100%;
		background-size: cover;
		&::before {
			content: '';
			@include posa(0, 0, 0, 0);
			background: #fff;
			opacity: 0;
			transition: .4s ease opacity;
			z-index: 9;
			.tile-list .tile-list-item-added & {
				background: $linkColor;
				opacity: .7;
			}
		}
	}

	&-link {
		&,
		&:hover {
			@include posa(0, 0, 0, 0);
			padding-left: 50%;
			color: #fff;
			text-decoration: none;
			display: flex;
			align-items: center;
		}
		&-content {
			text-align: center;
			margin: auto;

			.collection-item-change & {
				display: block;
				width: 100%;
			}
		}
	}
	&-name {
		position: relative;
		font-size: 25px;
		font-weight: 500;
		z-index: 8;
	}
	&-info {
		position: relative;
		display: block;
		font-size: 18px;
		overflow: hidden;
		max-height: 0;
		overflow: hidden;
		transition: .3s;
		line-height: 1.2;
		z-index: 8;
		small {
			font-size: 18px;
			display: block;
			margin: 0 0 20px;
		}
	}
}
