.notice {
  position: relative;
	display: flex;
	flex-direction: column;
	width: 100%;
	z-index: 9000;
	a {
		color: #fff;
		text-decoration: underline;
	}
	a.icon {
		text-decoration: none;
	}
	&-item {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 10px 30px;
		color: #fff;
		background-color: #e91e63;
		font-size: 16px;
	}
	&-msg {
		padding-right: 40px;
	}
	&-close {
		padding: 6px 10px;
		color: #fff;
		line-height: 14px;
		&:hover {
			color: #fff;
			text-decoration: none;
		}
	}
	&-item + &-item {
		border-top: 1px solid rgba(255, 255, 255, 0.3);
	}
}
