.checkout {
  display: flex;
  box-sizing: border-box;

  *,
  *::before,
  *::after {
    box-sizing: border-box;
  }

  &__left {
    width: calc(100% - 485px);

    @media (min-width: 1170px) {
      width: calc(100% - 570px);
    }
  }

  &__right {
    flex-shrink: 0;
    width: 485px;
    padding-left: 40px;

    @media (min-width: 1170px) {
      width: 570px;
      padding-left: 75px;
    }
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
  }
}
