// Размеры в шапке построены на EM, для того,
// чтобы пропорционально уменьшить все значения блока с описанием
.product {
	&-header {
		background: $bg-color;
		font-size: 18px; // Это базовое значение, от которого считаем EM
		.wrapper {
			position: relative;
			padding: 75px;
		}

		@media #{$desktop-small} {
			font-size: 13px;
			.wrapper {
				padding: 50px 40px;
				max-width: 1000px;
			}
		}
	}
	&-images {
		@include col(left, 49.62%, 0);
		position: relative;
		.item-tools {
			top: 20px;
		}
		.slick-slide:focus {
			outline:none;
		}
	}
	&-image-main {
		position: relative;
		.slide-img {
			padding-top: 100%;
			@media #{$desktop-small} {
				padding-top: 455px;
			}
		}
	}
	&-image-row {
		.slide-img {
			height: 125px;

			@media #{$desktop-small} {
				height: 90px;
			}
		}
	}
	.slide-img {
		position: relative;
		width: 100%;
		@extend %covered;

		&::before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background: #000;
			opacity: .1;
			transition: .4s ease opacity;
		}
	}
	&-image-row {
		margin: 0 -10px;
		padding-top: 20px;
		.slide {
			margin: 0 10px;
			opacity: 0.5;
			transition: opacity 0.2s ease;
			cursor: pointer;
			&:hover,
			&.slick-current {
				opacity: 1;
			}
		}
	}
	&-info {
		@include col(right, 50.38%, 0 0 75px);

		@media #{$desktop-small} {
			padding-left: 50px;
		}

		&-section {
			background: #fff;
			padding: em(30px);
			box-sizing: border-box;
		}
		&-main {
			min-height: 624px;
			margin-bottom: 20px;

			@media #{$desktop-small} {
				min-height: 455px;
			}
		}
		&-sold {
			margin-bottom: 20px;
		}
		&-sold-btn {
			text-align: center;
			h4 {
				color: #383838;
				font-size: 1.78em;
				font-weight: 500;
			}
			p {
				padding: 0.4em 2.4em 1.2em 2.4em;
				color: #383838;
				font-size: 0.95em;
				font-weight: 400;
				line-height: 1.56;
			}
			.product-button-actions {
				padding: 0;
			}
		}
		h1 {
			padding-bottom: em(30px, 32px);
			font-size: em(32px);
			font-weight: 500;
			color: $txt-black;
		}
		.price {
			padding-bottom: 1em;
			font-size: em(45px);
			font-weight: 700;
			color: $linkColor;
			&::after {
				content: attr(data-rub);
				font-weight: 400;
			}
			&-made-of {
				cursor: pointer;
				overflow: hidden;
				text-overflow: ellipsis;
			}
		}
		&-i {
			position: relative;
			padding-left: em(200px, 20px);
			margin-bottom: em(5px, 20px);
			color: $txt-black;
			font-size: em(20px);
			font-weight: 400;
			transition: height 0.2s ease;
			height: em(27px, 20px);
			&:not(.active) {
				height: em(27px, 20px) !important;
			}
			&-long {
				.price-made-of {
					position: relative;
					white-space: nowrap;
				}
				&.active {
					.price-made-of {
						white-space: normal;
					}
				}
				p {
					cursor: pointer;
				}
			}
			p {
				position: absolute;
				margin: 0;
				left: 0;
				top: 0;
				font-weight: 500;
				font-size: em(21px);
			}
			a {
				color: $txt-black;
				cursor: pointer;
			}
		}
		&-row {
			margin: 0 -7px;
			padding-top: em(40px);
		}
		.product-send {
			padding-top: em(35px, 20px);
			font-size: em(20px);
			font-weight: 400;
			color: $txt-black;
		}
	}
	hr {
		display: block;
		height: 2px;
		width: 100%;
		margin-top: em(35px);
		margin-bottom: 0;
		background: #dedede;
		border: none;
	}
	&-col {
		@include col(left, 50%, 7px);
		> span {
			display: block;
			padding-bottom: em(15px, 24px);
			font-size: em(24px);
			color: $txt-black;
			font-weight: 500;
		}
		.tools-item {
			width: 100%;
			margin: 0;
		}
		.content-toolbar-part {
			background: #fff;
			padding: 0;
			flex: none;
			border: 1px solid #dcdcdc;
			border-radius: 7px;
			height: em(50px);
			color: $txt-grey;
			&:after {
				top: em(22px);
				right: em(18px);
				border-top-color: #4c4c4c;
			}
			select {
				width: 100%;
				height: 100%;
			}
			.select2-selection--single {
				height: em(50px);
			}
			.select2-selection--single .select2-selection__rendered {
				line-height: em(47px);
				color: $txt-grey;
				font-size: em(18px);
				font-weight: 400;
			}
			.select2-selection__arrow {
				top: em(29px);
				right: em(12px);
			}
		}
		.content-toolbar-sort .select2-container--default .select2-selection--single {
			height: auto;
			border: none;
		}
	}
	&-button-row {
		padding-top: em(35px);
		.product-input {
			position: relative;
			float: left;
			width: 110px / 515px * 100%;
			margin-right: 25px / 515px * 100%;
			input {
				margin: 0;
				display: block;
				width: 100%;
				height: em(65px);
				font-size: em(18px);
				font-weight: 400;
				padding-left: 15px;
				padding-right: em(40px);
				color: $txt-grey;
				border-radius: 7px;
				border: solid 1px #dcdcdc;
				box-sizing: border-box;
			}
			i {
				content: '';
				position: absolute;
				right: em(18px);
				outline: none;
				width: 0;
				height: 0;
				border-style: solid;
				border-color: #fff transparent transparent transparent;
				transition: all .15s;
				cursor: pointer;
				user-select: none;
			}
			i.product-input-up {
				top: em(20px);
				border-width: 0 8px 8px;
				border-bottom-color: #4c4c4c;
				&:hover {
					opacity: 0.7;
				}
			}
			i.product-input-down {
				top: em(37px);
				border-width: 8px 8px 0;
				border-top-color: #4c4c4c;
				&.disabled,
				&:hover {
					opacity: 0.7;
				}
				&.disabled {
					opacity: 0.5;
					cursor: default;
				}
			}
		}
		.button {
			display: block;
			height: em(67px);
			width: 380px / 515px * 100%;
			float: left;
			line-height: normal;
			font-size: em(17px);
		}
	}
	&-button-actions {
		margin: 0 -7px;
		padding: em(5px) 0;
		.button {
			display: block;
			width: 100%;
			height: em(55px, 17px);
			padding: 0;
			line-height: em(55px, 17px);
			font-size: em(17px);
		}
		button.pink {
			background: #e91e63;
			&:hover {
				background: darken(#e91e63, 5%);
			}
		}
	}
	&-about {
		.review-item {
			border: none;
			margin-top: 0;
			&:focus {
				outline:none;
			}
		}
		.review-item > .review-list {
			margin-top: 0;
		}
		&-text {
			font-size: 18px;
		}
		&-autor {
			text-align: right;
			padding-top: 28px;
			img {
				display: block;
				float: right;
				margin-left: 30px;
				margin-bottom: 16px;
			}
			h2,
			p {
				margin: 0;
				padding: 0;
			}
			h2 {
				padding-top: 23px;
				a {
					line-height: 1.2;
					font-weight: 400;
					font-size: 18px;
					margin: 0;
					color: $txt-black;
				}
			}
			p a {
				line-height: 1.2;
				margin: 0;
				font-size: 18px;
				color: $textColor;
			}
		}
	}
	&-tags {
		margin: 0 -25px;
		padding-top: 30px;
		ul {
			display: table;
			width: 100%;
			font-size: 0;
			li {
				display: inline-block;
				list-style: none;
				padding: 0 20px;
				a {
					display: block;
					color: #899596;
					font-size: 18px;
					line-height: 2.5;
					font-weight: 400;
				}
			}
		}
	}
	&-gallery {
		background: $bg-color;
		.wrapper {
			padding-top: 75px;
			padding-bottom: 30px;
		}
		&-row {
			margin: 0 -22px / 1290px * 100%;
		}
		&-i {
			@include col(left, 25%, 22px / 1290px * 100%);
			margin-bottom: 45px;
		}
		&-img {
			display: block;
			position: relative;
			width: 100%;
			padding-bottom: 330px / 290px * 100%;
			cursor: pointer;
			@extend %covered;
			&:hover {
				h3,
				p {
					opacity: 1;
					transform: translateY(0);
				}
				p {
					transition-delay: 0.05s;
				}
				.product-gallery-overlay {
					opacity: 1;
				}
			}
		}
		&-overlay {
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			background-color: rgba(0, 0, 0, 0.5);
			opacity: 0;
			transition: all 0.3s ease;
			h3 {
				font-size: 25px;
				line-height: 20px;
				padding-bottom: 5px;
				color: #fff;
				font-weight: 500;
				opacity: 0;
				transform: translateY(15px);
				transition: all 0.2s ease;
				filter: blur(0);
			}
			p {
				margin: 0;
				font-size: 20px;
				color: #fff;
				opacity: 0;
				transform: translateY(15px);
				transition: all 0.2s ease;
				filter: blur(0);
			}
		}
		&-info {
			position: absolute;
			width: 100%;
			text-align: center;
			vertical-align: middle;
			top: 50%;
			transform: translateY(-50%);
			padding: 0 15px;
			box-sizing: border-box;
		}
		&-btn {
			background: $linkColor;
			color: #fff !important;
			font-size: 25px;
			font-weight: 500;
			.product-gallery-overlay {
				background: none;
				opacity: 1;
			}
			&:hover {
				background: #0d864b;
				transition: background 0.2s ease;
			}
		}
	}
	&-recently {
		text-align: center;
		.wrapper {
			padding-top: 75px;
			padding-bottom: 109px;
		}
		h4 {
			font-size: 32px;
			font-weight: 500;
			color: $txt-black;
		}
		&-slider {
			margin-top: 75px;
			padding: 0 83px;
			.slide {
				position: relative;
				cursor: pointer;
				&::after {
					content: "";
					display: block;
					position: absolute;
					top: 0;
					bottom: 0;
					right: 0;
					left: 0;
					background: rgba(0, 0, 0, 0.5);
					opacity: 0;
					transition: opacity 0.2s ease;
				}
				&:hover::after {
					opacity: 1;
				}
			}
		}
		a {
			display: block;
			margin: 0 12px;
			.slide {
				padding-bottom: 100%;
			}
		}
	}
	.select2-container .select2-results .select2-results__option {
		font-size: em(15px);
		@media #{$desktop-large} {
			font-size: em(20px);
		}
		&[aria-disabled="true"] {
			color: gray;
		}
	}
}