$color-border-light: #D8D8D8;
$color-border-dark: #676767;

$color-bg-dark: #777;
$color-bg-light: #EAEAEA;

div.tree-multiselect {
  width: 100%;
  font-weight: 400;
  max-height: 260px;
  overflow: auto;
  user-select: none;

  > div.selected,
  > div.selections {
    display: block;
    vertical-align: top;
    box-sizing: border-box;
    overflow: hidden;
  }

  > div.selections {
    border-right: solid 2px $color-border-light;

    div.item {
      line-height: 18px;
      padding-right: 10px;
    }

    &.no-border {
      border-right: none;
    }
  }

  > div.selected {
    > div.item {
      padding: 2px 5px;
      border-radius: 2px;
      background: $color-bg-light;
    }

    &.ui-sortable > div.item:hover {
      cursor: move;
    }
  }

  div.section {
    position: relative;
    &::before {

      position: absolute;
      left: 0;
      top: -1px;
      height: 2px;
      width: 120%;
      background: #f0f0f0;
    }
    > div.section,
    > div.item {
      padding-left: 20px;
      &::before {
        content: '';
        position: absolute;
        left: -500px;
        top: -1px;
        height: 2px;
        width: 1000px;
        background: #f0f0f0;
      }    
    }
  }

  div.title,
  div.item {
    position: relative;
    margin-left: 10px;
    padding: 10px 0;
    word-wrap: break-word;
  }

  div.title {
    color: #383838;
    line-height: 18px;
    padding-right: 40px;

    > * {
      display: inline-block;
    }

    > span.collapse-section {
      position: absolute;
      top: 14px;
      right: 12px;
      border-color: #383838 transparent transparent transparent;
      border-style: solid;
      border-width: 8px 8px 0;
      height: 0;
      width: 0;
      overflow: hidden;
    }

    &:hover {
      cursor: pointer;
    }
  }

  label {
    float: left;
  }

  input[type=checkbox] {
    display: none;
    margin-right: 5px;

    & + span {
      width: 16px;
      height: 16px;
      display: inline-block;
      position: relative;
      margin-right: 10px;
      border: solid 1px #d5d5d5;
      border-radius: 2px;
      cursor: pointer;
    }

    &:checked + span {
      border-color: #0f9d58;
      background: #0f9d58;
      &::before {
        content: '';
        border: 2px solid #fff;
        border-width: 0 0 3px 3px;
        position: absolute;
        width: 7px;
        height: 4px;
        transform: rotate(-45deg);
        left: 3px;
        top: 3px;
      }
    }
    &:indeterminate + span::before {
      content: '';
      position: absolute;
      left: 3px;
      top: 3px;
      right: 3px;
      bottom: 3px;
      background: #0f9d58;
    }

    &:hover {
      cursor: pointer;
    }
  }

  span.remove-selected,
  span.description {
    padding: 0 3px;
    margin-right: 5px;
    background: $color-bg-dark;
    color: white;
    border-radius: 2px;
  }

  span.remove-selected {
    &:hover {
      cursor: pointer;
    }
  }

  span.description {
    &:hover {
      cursor: help;
    }
  }

  div.temp-description-popup {
    background: $color-bg-light;
    padding: 5px;
    border: 2px solid $color-border-dark;
    border-radius: 3px;
  }

  span.section-name {
    float: right;
    font-style: italic;
  }

  .select-all-container {
    span.select-all,
    span.unselect-all {
      padding-right: 5px;
      margin-right: 5px;

      &:hover {
        cursor: pointer;
      }
    }

    span.select-all {
      border-right: 2px solid $color-border-light;
    }
  }
}
.tree-multiselect {
  &-button {
    height: 45px;
    background: none;
    border: 0;
    padding: 0 40px 0 15px;
    font-size: 18px;
    line-height: 43px;
    outline: 0;
    color: #fff;
    box-sizing: border-box;
    width: 100%;
    border: 1px solid rgba(255, 255, 255, 0.25);
    border-radius: 7px;
    position: relative;
    cursor: pointer;
    user-select: none;
    &::after {
      content: "";
      position: absolute;
      top: 18px;
      right: 12px;
      border-color: #fff transparent transparent transparent;
      border-style: solid;
      border-width: 8px 8px 0;
      height: 0;
      width: 0;
    }
    &.opened {
      background: #fff;
      color: #383838;
      &::after {
        border-top-color: #383838;
      }
      + .tree-multiselect-dropbox {
        opacity: 1;
        visibility: visible;
        margin-top: 8px;
      }
    }
  }
  &-dropbox {
    position: absolute;
    left: 0;
    right: 25px;
    z-index: 100;
    border-radius: 7px;
    box-sizing: border-box;
    background: #fff;
    margin: 0;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
    overflow: hidden;
    opacity: 0;
    visibility: hidden;
  }
}
