/* Main page */
.main {
    .header {
        background: url(../img/main.jpg);
        background-size: cover;
        background-position: 50% 50%;
        color: #fff;
        height: 100%;
        min-height: $main-page-header-height;
        max-height: $page-header-max-height;
        box-sizing: border-box;
        text-align: center;
        position: relative;
        display: flex;
        align-items: center;
        @media only screen and (min-device-width : 320px) {
            height: 570px;
        }
        @media only screen and (min-device-width : 375px) {
            height: 665px;
        }
        @media only screen and (min-device-width : 768px) {
            height: 100%;
        }

        .wrapper {
            position: relative;
            padding-bottom: 70px;
        }
        &-title {
            margin: 0 0 28px;
            font-size: 57px;
            line-height: 1.05;
            font-weight: 400;
            span {
                display: block;
            }
        }
        &-subtitle {
            margin: 0 0 40px;
            font-size: 24px;
        }
        &-button {
            height: 65px;
            line-height: 65px;
            text-transform: uppercase;
        }
    }
    .video {
        @include posa(0, 0, 0, 0);
        overflow: hidden;
        &::after {
            content: '';
            @include posa(0, 0, 0, 0);
            background: rgba(0, 0, 0, .5);
        }
        video,
        .poster {
            min-width: 100%;
            min-height: 100%;
            width: auto;
            height: auto;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            background: url(../img/main.jpg);
            background-size: cover;
            background-position: 50% 50%;
        }
    }
    .nav {
        position: absolute;
        width: 100%;
        bottom: -1px;
        left: 0;
        background: rgba(0, 0, 0, .5);
        border-bottom: 50px solid #fff;
        &:after {
          position: absolute;
          display: block;
          content: '';
          bottom: -52px;
          width: 100%;
          height: 50px;
          background-color: #fff;
        }
        &-list {
            padding: 0 20px !important;
            list-style: none;
            display: flex;
        }
        &-item {
            flex: 0 0 8.33%;
            position: relative;
            border: 1px solid rgba(255, 255, 255, .3);
            border-width: 0 1px 0 0;
            box-sizing: border-box;
            &:first-child {
                border-width: 0 1px 0 1px;
            }
            img {
                width: 100%;
            }
        }
        &-link {
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            color: #fff;
            display: flex;
            justify-content: center;
            align-items: top;
            &:hover {
                color: #fff;
                text-decoration: none;
                background: #ff5722;
                transform: scale(1.27, 1.22);
                z-index: 5;
                .icon {
                    transform: scale(.96, 1) translate(0, -50%);
                    &::before {
                        margin: 0 0 10px;
                    }
                    span {
                        display: block;
                        font-size: 14px;
                        opacity: 1;
                    }
                }
            }
            .icon {
                position: absolute;
                left: 0;
                width: 100%;
                top: 50%;
                transform: translate(0, -50%);
                &::before {
                    display: block;
                    font-size: 55px;
                    margin: 0;
                }
                span {
                    display: none;
                    padding: 0 7px;
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 1;
                    opacity: 0;
                }
            }
        }
    }

    .capabilities {
        &.dark {
            background: #f3f3f3;
        }

        .wrapper {
            background: url(../img/laptop.svg) 51% 30% no-repeat;
            background-size: 49% auto;
            height: 450px;
            position: relative;
        }
        &-block {
            position: absolute;
            &-1 {
                right: 70.5%;
                top: 40px;
                left: 0;
                text-align: right;
                padding-left: 20px;
                .capabilities-text {
                    max-width: 320px;
                    margin-left: auto;
                }
            }
            &-2 {
                right: 75.5%;
                bottom: 70px;
                left: 0;
                text-align: right;
                min-height: 170px;
                padding-left: 20px;
            }
            &-3 {
                left: 76.5%;
                top: 30px;
                right: 0;
                padding-right: 20px;
                .capabilities-text {
                    max-width: 300px;
                }
            }
            &-4 {
                left: 70.5%;
                bottom: 70px;
                right: 0;
                padding-right: 20px;
                min-height: 120px;
                .capabilities-text {
                    max-width: 345px;
                }
            }
        }
        &-title {
            color: #383838;
            font-weight: 500;
            margin: 0 0 10px;
            font-size: 21px;
        }
        &-text {
            line-height: 1.3;
            margin: 0;
        }
    }

    .info {
        background: #f3f3f3;
        .wrapper {
            padding: 50px;
            display: flex;
            justify-content: space-between;
        }
        &-block {
            width: 29%;
            max-width: 380px;
            position: relative;
            text-align: center;
            color: #fff;
            &::before {
                content: '';
                @include posa(0, 0, 0, 0);
                background: rgba(0, 0, 0, .15);
            }
            &-1 .button {
                background: #e91e63;
                &:hover {
                    background: darken(#e91e63, 5%);
                }
            }
            &-2 .button {
                background: #03a9f4;
                &:hover {
                    background: darken(#03a9f4, 5%);
                }
            }
            &-3 .button {
                background: #ffc107;
                &:hover {
                    background: darken(#ffc107, 5%);
                }
            }
        }
        &-title {
            position: absolute;
            top: 30px;
            left: 10%;
            right: 10%;
            font-size: 25px;
            height: 6em;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0;
            z-index: 2;
        }
        &-text {
            position: absolute;
            bottom: 105px;
            left: 12%;
            right: 12%;
            font-size: 17px;
            line-height: 1.3;
            margin: 0;
            z-index: 2;
        }
        &-button {
            position: absolute;
            bottom: 30px;
            left: 17%;
            right: 17%;
            z-index: 2;
            .button {
                padding: 0;
                width: 100%;
                text-align: center;
                white-space: nowrap;
            }
        }
    }

    .choice {
        position: relative;
        &-title {
            padding: 75px 0 50px;
            font-size: 45px;
            color: #383838;
            margin: 0;
            text-align: center;
        }
    }

    @import "partials/brand";

    .goods-of-day {
        .wrapper {
            padding: 75px 50px 50px;
        }
        &-title {
            text-align: center;
            color: #383838;
            font-size: 45px;
            margin: 0 0 50px;
        }
        .goods-list {
            margin: 50px 0 25px;
        }
        &-tools {
            margin-top: 50px;
            text-align: center;
        }
    }

    .reviews {
        background: #a1887f;
        color: #fff;
        .wrapper {
            padding: 50px 70px;
        }
        a,
        a:hover {
            color: #fff;
        }
        &-title {
            font-size: 45px;
            text-align: center;
            margin: 0 0 20px;
        }
        &-list {
            margin: 0;
            padding: 0;
            list-style: none;
        }
        &-item {
            margin: 35px 0 0;
            min-height: 100px;
            position: relative;
            padding-left: 150px;
        }
        &-image {
            position: absolute;
            left: 10px;
            top: 0;
            width: 100px;
            height: 100px;
        }
        &-author {
            font-weight: bold;
            font-size: 25px;
        }
        &-text {
            margin: 15px 0 0;
            line-height: 1.56;
            p {
                margin: 15px 0 0;
            }
        }
        &-tools {
            margin: 50px 0 0;
            text-align: right;
            line-height: 55px;
        }
    }

    .how-it-works {
        text-align: center;
        transform: translateZ(0);
        .wrapper {
            padding: 100px 0;
        }
        &-block {
            position: relative;
            display: inline-block;
            vertical-align: top;
            width: 30%;
            margin: 0 0 0 3%;
            padding-top: 160px;
            &:first-child {
                margin: 0;
            }
            &::before {
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                text-align: center;
            }
            &.icon-diamond::before {
                top: 7px;
                color: #03a9f4;
                font-size: 159px;
            }
            &.icon-security::before {
                top: 0;
                color: #ffc107;
                font-size: 151px;
            }
            &.icon-make::before {
                top: 7px;
                color: #e91e63;
                font-size: 142px;
            }
            &.icon-messaging::before {
                top: 9px;
                color: #0f9d58;
                font-size: 147px;
            }
        }
        &-title {
            display: flex;
            justify-content: center;
            align-items: center;
            color: #0f9d58;
            font-size: 25px;
            line-height: 30px;
            min-height: 60px;
            margin: 15px 0 0;
        }
        &-text {
            color: #000;
            font-size: 18px;
            line-height: 25px;
            margin: 10px 0 0;
        }
    }
    .fader {
        width: 100%;
        min-width: 1000px;
        height: 0;
        position: relative;
        overflow: hidden;
        // transition: height ease 1s;
        .close {
            position: absolute;
            right: 30px;
            top: 30px;
            width: 24px;
            height: 24px;
            font-size: 24px;
            line-height: 24px;
            cursor: pointer;
        }
        .how-it-works {
            position: absolute;
            left: 0;
            bottom: 0;
            right: 0;
        }
    }
}