.alert {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  min-height: 95px;
  padding: 20px 25px;
  margin: 50px 0;
  &-title {
    margin-bottom: 10px;
    font-size: 18px;
    font-weight: 500;
  }
  &-title,
  &-text {
    line-height: 1.39;
  }
  &.warning {
    color: #fff;
    background-color: #cd0a0a;
    .alert-text {
      font-size: 16px;
    }
  }
  &.error {
    justify-content: center;
    margin-bottom: 20px;
    color: #cd0a0a;
    background-color: #f3f3f3;
    .alert-text {
      padding: 10px 0;
      font-size: 18px;
      text-align: center;
    }
  }
  &.error + &.error {
    margin-top: 0;
  }
  & + .form-fieldset {
    margin-top: 50px;
  }
}