.feedback {
  padding: 20px;
  background-color: #fff;
  &-img {
    float: left;
    width: 100px;
  }
  &-txt {
    float: right;
    width: calc(100% - 137px);
  }
  &-title {
    margin-bottom: 15px;
    font-size: 25px;
    font-weight: 700;
  }
  &-body {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.56;
  }
}