.orders {

  .wrapper {
    min-width: auto;
  }

  .content-toolbar {
    margin-bottom: 10px;
  }

  .content-toolbar-tabs a:not(:first-child) {
    margin-left: 114px;
    @media only screen and (max-width: 1150px) {
      margin-left: 40px;
    }
  }

  .content-toolbar-part.content-toolbar-tabs {
    flex: 0 1 42%;
  }

  .carousel-title {
    width: calc(100% - 84px);
    padding-left: 84px;

    &:before {
      color: $linkColor;
    }

    &:hover p {
      text-decoration: underline;
    }
  }

  .carousel-title-text {
    float: left;
    width: 27%;
    @media only screen and (max-width: 1150px) {
      width: 24%;
      font-size: 16px;
    }
  }

  .carousel-container {
    padding: 35px 34px;
  }

  &-quick-info {
    width: 35%;
    float: left;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 90px;
    
    p {
      margin: 0 25px 0 0;
      color: $txt-black; 
      line-height: 20px;
      @media only screen and (max-width: 1150px) {
        font-size: 16px;
      }
    }
  }

  &-contact-btn {
    display: flex;
    align-items: center;
    position: absolute;
    top: 0;
    right: 35px;
    height: 90px;
  }

  hr {
    top: 90px;
  }

  &-info {
    float: left;
    width: calc(100% - 430px);
    @media only screen and (max-width: 1150px) {
      width: calc(100% - 360px);
    }
    &-title {
      font-size: 24px;
      font-weight: 500;
    }
    &-subtitle {
      margin: 15px 0;
      font-size: 20px;
      font-weight: 500;
      p {
        font-weight: 400;
        span {
          font-weight: 500;
        }
      }
    }
    &-address {
      display: block;
      list-style: none;
      color: #4c4c4c;
      li {
        padding: 2px 0;
      }
    }
    .form-field {
      max-width: 580px;
      height: 52px;
      + .orders-info-inputs {
        margin-top: 5px;
      }
    }
    &-inputs {
      max-width: 580px; 
      margin-top: 20px;
      > div {
        &:nth-child(1) {
          width: 100%;
        }
        &:nth-child(2) {
          float: left;
          width: calc(50% - 10px);
          .form-field {
            max-width: 280px;
          }
        }
        &:nth-child(3) {
          float: right;
          width: calc(50% - 10px);
          .form-field {
            max-width: 280px;
          }
          .button {
            width: 100%;
            white-space: normal;
            line-height: 18px;
          }
        }
        &:nth-child(4) {
          width: calc(50% - 10px);
          .button {
            width: 100%;
            margin: 20px 0;
          }
        }
      }
      .orders-info-subtitle {
        @media only screen and (max-width: 1200px) {
          font-size: 16px; 
        }
      }
      .button {
        margin: 0;
        padding: 0 20px;
      }
      + .orders-cancel {
        margin-top: 25px;
      }
    }
    p {
      color: #4c4c4c;
      line-height: 1.38;
      + .clearfix {
        margin-top: 25px;
      }
    }
    .feedback {
      margin-top: 30px;
    }
    &-feedback {
      margin: 35px 0;
      .feedback {
        margin-top: 0;
      }
    }
    .icon-money {
      color: #ffc107;
      font-size: 300px; 
    }
  }

  &-product {
    box-sizing: border-box;
    position: relative;
    float: right;
    width: 310px;
    padding: 0;

    .img-counter {
      position: absolute;
      top: 10px;
      left: 50%;
      color: #fff;
      font-size: 18px;
      transform: translateX(-50%);
      z-index: 10;
      user-select: none;
    }

    .slick-arrow {
      top: 148px;
    }

    .slick-slide {
      width: 310px;
    }

    .slide-img {
      position: relative;
      width: 310px;
      height: 296px;
      background-size: cover;
      background-position: 50% 50%;
      background-repeat: no-repeat;
      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0, 0, 0, .15);
      }
    }

    &-details,
    &-cost {
      padding: 15px;
      background-color: #fff;
      table {
        width: 100%;
        td {
          padding: 3px 0;
        }
        tr td:nth-child(1) {
          width: 65%;
          font-weight: 500;
        }
      }
    }
    &-details {
      width: 310px;
    }
    &-cost {
      margin-top: 20px;
      tr:nth-child(3) td:nth-child(2) {
        font-size: 18px;
        font-weight: 500;
        color: #0f9d58;
      }
    }

    &-title {
      margin-bottom: 14px;
      font-size: 20px;
      font-weight: 500;
      text-align: center;
      color: #0f9d58;
    }
  }

  &-cancel {
    &-link {
      color: #ff0000;
      font-size: 17px;
      line-height: 34px;
      &:hover {
        color: #ff0000;
      }
      .is-canceled & {
        display: none;
      }
    }
    &-form {
      display: none;
      margin-top: 40px;
      .is-canceled & {
        display: block;
      }
      p {
        color: #4c4c4c;
        font-size: 16px;
      }
    }
    .button:nth-child(1) {
      width: 280px;
      padding: 0;
      margin-right: 16px;
      @media only screen and (max-width: 1150px) {
        width: 200px;
      }
    }
    + p {
      margin-top: 30px;
    }
  }

  .textarea {
    width: auto;
    margin-bottom: 20px;
    padding: 15px;
    background-color: #fff;
    textarea {
      height: 120px;
      padding: 0;
      font-size: 18px;
    }
  }

  .width-l {
    width: 280px;
  }

  &-location {
    display: flex;
    padding-top: 20px;
    p {
      margin: 0 33px 0 0;
      font-size: 20px;
      line-height: 50px;
    }
    .button {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 280px;
      white-space: normal;
      line-height: 19px;
      padding: 0;
    }
  }

  &-meter {
    position: relative;
    padding: 30px 0;
    &:before {
      position: absolute;
      display: block;
      content: '';
      width: calc(100% + 68px);
      height: 2px;
      margin: 0 -34px;
      background-color: #fff;
    }
  }

}
