/* Information */

.information {
  // COMMON
  &__logo {
    width: 311px;
    height: 67px;
    margin-bottom: 1rem;
    background-image: url('../img/mellroot-logo_green.svg');
    background-repeat: no-repeat;
    background-size: 100%;
    @media #{$desktop-up} {
      margin-bottom: 0;
    }
  }
  &.shop-owners,
  &.buyers {
    color: $txt-black;
    line-height: 25px;
  }
  .section-title {
    margin-bottom: 50px;
    font-size: 32px;
    line-height: 1.3;
    font-weight: 500;
    text-align: center;
  }
  .section-centered-content {
    max-width: 800px;
    margin: auto;
    text-align: center;
  } //
  // ABOUT PAGE
  //
  .about-matbets {
    >.wrapper {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-flow: column wrap;
      max-width: 1110px;
      padding: 47px 0;
      @media #{$desktop-up} {
        flex-flow: row wrap;
        padding: 94px 0;
      }
    }
    .text-block {
      max-width: 626px;
      font-size: 24px;
      color: $txt-black;
      text-align: center;
    }
  }
  .capabilities {
    padding-top: 55px;
    height: 500px;
    box-sizing: border-box;
  }
  &.about .how-it-works {
    box-sizing: border-box;
    @media (min-width: 1024px) {
      height: 530px;
    }
  }
  .our-team {
    padding: 81px 0;
    box-sizing: border-box;
    background: #a1887f;
    @media (min-width: 1024px) {
      height: 684px;
    }
    >.wrapper {
      overflow: hidden;
      padding-left: 0;
      padding-right: 0;
    }
    .our-team-title {
      margin-bottom: 96px;
      color: #fff;
    }
    .guys {
      display: flex;
      justify-content: space-around;
      align-items: flex-end;
      margin-left: -6rem;
    }
    .guy {
      position: relative;
      list-style: none;
      background-size: 100%;
      background-repeat: no-repeat;
      user-select: none;
      &.sergey {
        width: 298px;
        height: 394px;
        background: url('../img/illustration-guys.svg') 48.598130841121495% 100% no-repeat;
      }
      &.dmitry {
        width: 161px;
        height: 378px;
        margin-left: -12rem;
        background: url('../img/illustration-guys.svg') 83.03571428571429% 96.24413145539906% no-repeat;
      }
      &.emre {
        width: 202px;
        height: 410px;
        margin-left: -12rem;
        background: url('../img/illustration-guys.svg') 0 0 no-repeat;
      }
      &.pasha {
        width: 260px;
        height: 394px;
        margin-left: 10rem;
        background: url('../img/illustration-guys.svg') 0 100% no-repeat;
      }
      &.katya {
        width: 295px;
        height: 410px;
        background: url('../img/illustration-guys.svg') 37.54646840148699% 0 no-repeat;
      }
      &.yury {
        width: 336px;
        height: 410px;
        background: url('../img/illustration-guys.svg') 100% 0 no-repeat;
      }
    }
    .guy-info {
      position: absolute;
      top: -84px;
      z-index: 1;
      padding-top: 50px;
      padding-left: 35px;
      opacity: 0;
      transition: opacity .25s;
      &,
      &:before {
        width: 240px;
        height: 137px;
      }
      &:before {
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        font-size: 240px;
        line-height: .575;
        color: #fff;
      }
    }
    .guy:hover .guy-info,
    .guy.active .guy-info {
      opacity: 1;
    }
    .guy.sergey .guy-info {
      left: 208px;
    }
    .guy.dmitry .guy-info {
      left: 128px;
    }
    .guy.emre .guy-info {
      left: 128px;
    }

    .guy.pasha .guy-info {
      left: 128px;
      padding-left: 40px;
    }
    .guy.katya .guy-info {
      left: 155px;
      padding-top: 40px;
      padding-left: 43px;
    }
    .guy.yury .guy-info {
      left: 168px;
      padding-top: 53px;
      padding-left: 45px;
      @media (max-width: 1000px) {
        left: 95px;
      }
    }
    .guy-name {
      margin-bottom: .25rem;
      font-size: 21px;
      color: $txt-black;
    }
    .guy-position {
      margin: 0;
    }
  } //
  // SHOP OWNERS PAGE
  //
  .about-talent {
    padding: 75px 0;
    text-align: center;
    .about-talent-title {
      margin-bottom: 31px;
      font-size: 45px;
      line-height: 56px;
    }
    .about-talent-text {
      max-width: 830px;
      margin: 0 auto 36px;
      font-size: 24px;
      line-height: 1.3;
    }
    .about-talent-button {
      width: 260px;
      height: 65px;
      line-height: 65px;
    }
  }
  .header-button {
    width: 220px;
    height: 55px;
    padding: 0;
    line-height: 55px;
    text-transform: none;
  }
  .publish-steps {
    height: 910px;
    padding: 81px 0 77px;
    box-sizing: border-box;
    background: #f3f3f3;
    .wrapper {
      min-height: 755px;
      background-image: url(../img/shop-illustration-1.svg);
      background-position: center 50px;
      background-repeat: no-repeat;
      background-size: auto 60%;
      @media (min-width: 1150px) {
        background-position: center 0;
      }
      @media (min-width: 1300px) {
        background-size: auto 755px;
      }
    }
    .publish-steps-container {
      display: flex;
      justify-content: space-between;
      flex-flow: row wrap;
    }
    .publish-steps-column {
      width: 33%;
      max-width: 375px;
      margin-left: 2.6%;
      @media only screen and (max-width: 1150px) {
        margin-left: 0.7%;
      }
    }
    .publish-step-1 {
      .subtitle-1:before {
        top: 1.5rem;
        right: -13.5rem;
        width: 389px;
        height: 69px;
        background-image: url(../img/shop-illustration-curve-1.svg);
        background-size: 66%;
        transform: rotate(4deg);
        @media (min-width: 1150px) {
          top: -1rem;
          right: -12.5rem;
          background-size: 75%;
          transform: rotate(-7deg);
        }
        @media (min-width: 1300px) {
          top: .5rem;
          right: -38%;
          background-size: auto;
          transform: none;
        }
      }
      .subtitle-2:before {
        top: -6.7rem;
        right: -21.7rem;
        width: 405px;
        height: 132px;
        background-image: url(../img/shop-illustration-curve-2.svg);
        background-size: 61%;
        transform: rotate(-14deg);
        @media (min-width: 1150px) {
          top: -8.75rem;
          right: -20rem;
          background-size: 75%;
          transform: rotate(-18deg);
        }
        @media (min-width: 1300px) {
          top: -7.25rem;
          right: -18rem;
          background-size: auto;
          transform: rotate(-2deg);
        }
      }
    }
    .publish-step-2 {
      .publish-step-subtitle:before {
        top: .9rem;
        left: -5.5rem;
        width: 141px;
        height: 163px;
        background-image: url(../img/shop-illustration-curve-3.svg);
        background-size: 73%;
        transform: rotate(-14deg);
        @media (min-width: 1150px) {
          top: .5rem;
          left: -6.5rem;
          background-size: 65%;
          transform: none;
        }
        @media (min-width: 1300px) {
          top: .6rem;
          left: -9rem;
          background-size: auto;
        }
      }
    }
    .publish-step-3 {
      .publish-step-subtitle:before {
        top: -5.6rem;
        left: -9.9em;
        width: 317px;
        height: 136px;
        background-image: url(../img/shop-illustration-curve-4.svg);
        background-size: 67%;
        transform: rotate(27deg);
        @media (min-width: 1150px) {
          top: -8rem;
          left: -15rem;
          background-size: 83%;
          transform: rotate(23deg);
        }
        @media (min-width: 1300px) {
          top: -7.3rem;
          left: -20.3rem;
          transform: rotate(3deg);
          background-size: 100%;
        }
      }
    }
    .publish-step-4 {
      .subtitle-1:before {
        top: -1.4rem;
        left: -6rem;
        width: 141px;
        height: 63px;
        background-image: url(../img/shop-illustration-curve-5.svg);
        background-size: 66%;
        -webkit-transform: rotate(30deg);
        transform: rotate(17deg);
        @media (min-width: 1150px) {
          top: -4rem;
          left: -8rem;
          background-size: auto;
          transform: rotate(30deg);
        }
        @media (min-width: 1300px) {
          top: -2.3rem;
          left: -9.2rem;
          transform: none;
        }
      }
    }
    .publish-step-title {
      margin-bottom: 20px;
      font-size: 24px;
      color: #e91e63;
    }
    .publish-step-subtitle {
      position: relative;
      margin-bottom: 10px;
      font-size: 21px;
      &:before {
        position: absolute;
        content: '';
        background-repeat: no-repeat;
      }
    }
    .publish-step-content {
      margin-bottom: 40px;
      color: #7f8c8d;
    }
  }
  &.shop-owners {
    .how-it-works {
      padding: 75px 0;
      .icon-shop-on:before {
        top: 0;
        font-size: 142px;
        color: #03a9f4;
      }
      .icon-calendar:before {
        top: 4px;
        font-size: 131px;
        color: #e91e63;
      }
      .icon-transport:before {
        top: -6px;
        font-size: 153px;
        color: #303f9f;
      }
      .icon-money:before {
        top: -1px;
        font-size: 141px;
        color: #ffc107;
      }
      .wrapper {
        display: flex;
        justify-content: space-between;
        max-width: 1500px;
        padding: 0;
      }
      .how-it-works-block {
        position: relative;
        width: 25%;
        margin: 0;
        padding: 150px 1rem 0;
        &:before {
          display: block;
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
        }
      }
      .how-it-works-main-title {
        margin-bottom: 80px;
        font-size: 32px;
        color: $txt-black;
      }
      .how-it-works-title {
        min-height: 0;
        font-size: 21px;
        color: $txt-black;
      }
      .how-it-works-text {
        font-size: 16px;
      }
      .button {
        margin-top: 50px;
      }
    }
  }
  .payment-policy {
    box-sizing: border-box;
    height: 410px;
    padding: 75px 0 125px;
    color: #fff;
    text-align: center;
    background: #617680 url('../img/element-coins.svg') center bottom no-repeat;
    ;
    .payment-policy-title {
      margin-bottom: 30px;
    }
    p {
      margin: 1.65em 0;
    }
  }
  .what-to-sell {
    box-sizing: border-box;
    height: 450px;
    padding: 75px 0;
    *,
    *:before,
    *:after {
      box-sizing: border-box;
    }
    .what-to-sell-title {
      margin-bottom: 50px;
      font-size: 32px;
      text-align: center;
    }
    .what-to-sell-content {
      display: flex;
      justify-content: space-between;
      flex-flow: row wrap;
    }
    .what-to-sell-about {
      box-sizing: border-box;
      width: 40%;
      padding: 0 20px;
    }
    .what-to-sell-examples {
      display: flex;
      justify-content: flex-end;
      width: 60%;
      margin-top: -.25rem;
    }
    .examples-list {
      position: relative;
      width: 42%;
      min-width: 270px;
      text-align: center;
      &:first-child {
        padding-right: 40px;
        &:before {
          position: absolute;
          top: 0;
          right: 0;
          width: 1px;
          height: 140px;
          background: rgba(0, 0, 0, .1);
          content: '';
        }
        @media only screen and (max-width: 1200px) {
          padding-right: 20px;
        }
      }
      &:last-child {
        padding-left: 40px;
        @media only screen and (max-width: 1200px) {
          padding-left: 20px;
        }
      }
    }
    .examples-container {
      position: relative;
      min-height: 170px;
      >div {
        display: flex;
        justify-content: space-around;
        align-items: center;
      }
    }
    .example-item {
      width: 64px;
      margin: 10px 0;
      line-height: 1;
      &:before {
        font-size: 43px;
      }
    }
    .icon-clothing:before {
      font-size: 45px;
    }
    .icon-furniture:before {
      font-size: 56px;
    }
    .icon-pottery:before {
      font-size: 44px;
    }
    .icon-robot:before {
      font-size: 65px;
    }
    .icon-toys:before {
      font-size: 55px;
    }
    .icon-sewing:before {
      font-size: 45px;
    }
    .icon-craft:before {
      font-size: 52px;
    }
    .icon-papers:before {
      font-size: 62px;
    }
    .icon-buttons:before {
      font-size: 47px;
    }
    .icon-mech:before {
      font-size: 47px;
    }
    .examples-title {
      font-size: 21px;
      font-weight: 500;
    }
  }
  .commercial-policy {
    padding: 65px 0 0;
    color: #fff;
    text-align: center;
    background: #8bc34a;
    @media (min-width: 1024px) {
      padding-bottom: 0;
    }
    @media (min-width: 1110px) {
      height: 370px;
    }
    @media (max-width: 1110px) {
      padding-bottom: 65px;
    }
    .wrapper {
      max-width: 1500px;
    }
    .commercial-policy-title {
      margin-bottom: 67px;
    }
    .policy-statements {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      list-style: none;
      >li {
        flex-basis: 25%;
        &:first-child {
          max-width: 220px;
        }
        &:nth-child(2),
        &:nth-child(3) {
          max-width: 261px;
        }
        &:last-child {
          max-width: 361px;
          text-align: left;
          @media (max-width: 1110px) {
            order: -1;
            width: 100%;
            max-width: 100%;
            flex-basis: 100%;
            margin-bottom: 43px;
            padding: 0 10%;
            text-align: center;
          }
        }
      }
    }
    .policy-statement-title {
      height: 72px;
      font-size: 72px;
      font-weight: 900;
      line-height: 1;
    }
    .policy-statement-subtitle {
      font-size: 21px;
      font-weight: 500;
    }
    .policy-statement-text {
      max-width: 260px;
      margin: auto;
    }
    .secondary-text {
      color: #f1f1f1;
    }
  }
  .custom-conditions {
    box-sizing: border-box;
    padding: 75px 0 97px;
    @media (min-width: 1024px) {
      height: 410px;
    }
  }
  .online-management {
    box-sizing: border-box;
    height: 410px;
    padding-top: 75px;
    color: #fff;
    background: #673ab7 url('../img/device.svg') 90% bottom no-repeat;
    @media (min-width: 1110px) {
      background-position: 80% bottom;
    }
    .online-management-content {
      max-width: 600px;
      @media (min-width: 1366px) {
        margin-left: 3rem;
      }
    }
  }
  .just-simple {
    box-sizing: border-box;
    padding: 75px 0;
    color: #fff;
    background: #a1887f;
    @media (min-width: 1024px) {
      height: 500px;
    }
    .wrapper {
      max-width: 1500px;
    }
    .just-simple-content {
      display: flex;
      justify-content: space-between;
      flex-flow: row wrap;
    }
    .just-simple-row {
      display: flex;
      justify-content: space-between;
      margin-bottom: 57px;
    }
    .just-simple-item {
      display: flex;
      align-items: center;
      width: 45%;
    }
    .just-simple-link {
      font-weight: 600;
      color: #fff;
      &:hover {
        text-decoration: underline;
      }
    }
    .icon {
      flex: 1 0 130px;
      width: 130px;
    }
    .icon-target:before {
      font-size: 100px;
    }
    .icon-search:before {
      font-size: 92px;
    }
    .icon-lifesaver:before {
      font-size: 91px;
    }
    .icon-talk:before {
      font-size: 92px;
    }
  }
  .call-to-action {
    padding: 75px 0;
    text-align: center;
    .call-to-action-content {
      max-width: 960px;
      margin: 0 auto 37px;
      font-size: 21px;
      line-height: 35px;
      font-weight: 500;
    }
  } //
  // BUYERS
  //
  .how-it-works {
    .icon-messaging {
      color: #0f9d58;
    }
  }
  &.buyers .how-it-works {
    box-sizing: border-box;
    @media (min-width: 1024px) {
      height: 630px;
    }
    .wrapper {
      padding: 75px 0;
    }
    .how-it-works-block {
      margin: 0 1.5% 0 1.5%;
    }
    .how-it-works-lead-title {
      margin-bottom: 75px;
      font-size: 32px;
      font-weight: 500;
      line-height: 38px;
      color: #383838;
      text-align: center;
    }
    .icon-diamond:before {
      display: block;
      margin-top: .75rem;
      margin-bottom: -.25rem;
      font-size: 152px;
    }
  }
  .purchase-steps {
    box-sizing: border-box;
    padding: 68px 0 8px;
    color: #fff;
    background: #617680;
    @media (min-width: 1024px) {
      min-height: 721px;
    }
    .wrapper {
      max-width: 1500px;
    }
    .purchase-steps-title {
      margin-bottom: 64px;
    }
    .purchase-steps-list {
      display: flex;
      justify-content: space-between;
      flex-flow: row wrap;
      &.first {
        .purchase-step {
          width: 25%;
        }
      }
    }
    .purchase-step {
      position: relative;
      width: 30%;
      padding-bottom: 60px;
      background: 100% 43px no-repeat;
      &:before {
        position: absolute;
        content: '';
      }
      .purchase-step-icon {
        position: relative;
        &:after {
          position: absolute;
          content: '';
        }
      }
      &.purchase-step-1 {
        margin-left: 1%;
        .purchase-step-icon {
          height: 90px;
          font-size: 96px;
          &:before {
            position: relative;
            top: 2px;
          }
          &:after {
            top: 83px;
            right: -115px;
            width: 99px;
            height: 190px;
            background-image: url('../img/information-buyers-arrow-1.svg');
            @media (min-width: 1024px) {
              top: 53px;
              right: -130px;
            }
          }
        }
        .purchase-step-text {
          max-width: 300px;
        }
      }
      &.purchase-step-2 {
        width: 31%;
        margin-left: 6%;
        .purchase-step-icon {
          font-size: 80px;
          &:after {
            top: -73px;
            right: -285px;
            width: 282px;
            height: 125px;
            background: url('../img/information-buyers-arrow-2.svg') no-repeat;
            background-size: 85%;
            transform: rotate(-10deg);
            @media (min-width: 1200px) {
              top: -66px;
              right: -302px;
              background-size: auto;
              transform: none;
            }
          }
        }
        .purchase-step-text {
          max-width: 437px;
        }
      }
      &.purchase-step-3 {
        margin-top: 20px;
        .purchase-step-icon {
          font-size: 76px;
          &:before {
            position: relative;
            top: -7px;
          }
          &:after {
            top: 62px;
            right: -105px;
            width: 102px;
            height: 193px;
            background: url('../img/information-buyers-arrow-3.svg') no-repeat;
            @media (min-width: 1024px) {
              top: 42px;
              right: -120px;
            }
          }
        }
        .purchase-step-text {
          max-width: 284px;
        }
      }
      &.purchase-step-4 {
        box-sizing: border-box;
        margin-left: 3%;
        .purchase-step-icon {
          font-size: 92px;
          &:after {
            top: -75px;
            right: -225px;
            width: 215px;
            height: 125px;
            background: url('../img/information-buyers-arrow-4.svg') no-repeat;
            transform: rotate(-10deg);
            @media (min-width: 1024px) {
              top: -60px;
              right: -246px;
              transform: none;
            }
          }
        }
        .purchase-step-text {
          max-width: 349px;
        }
      }
      &.purchase-step-5 {
        margin-top: 21px;
        margin-right: 9%;
        .purchase-step-icon {
          &:before {
            font-size: 75px;
            position: relative;
            top: -4px;
          }
          &:after {
            top: 62px;
            right: -125px;
            width: 125px;
            height: 197px;
            background: url('../img/information-buyers-arrow-5.svg') no-repeat;
            @media (min-width: 1024px) {
              top: 32px;
              right: -140px;
            }
          }
        }
        .purchase-step-text {
          max-width: 265px;
        }
      }
      &.purchase-step-6 {
        box-sizing: border-box;
        padding-left: 3%;
        .purchase-step-icon {
          &:before {
            font-size: 90px;
          }
        }
        .purchase-step-text {
          max-width: 265px;
          padding-left: 17px;
        }
      }
    }
    .purchase-step-icon {
      width: 94px;
      height: 71px;
      margin: 0 auto 20px;
      font-size: 71px;
      text-align: center;
    }
    .purchase-step-2,
    .purchase-step-4,
    .purchase-step-6 {
      .purchase-step-icon {
        margin-bottom: 30px;
      }
    }
    .purchase-step-text {
      max-width: 340px;
      margin: auto;
      ul {
        list-style: none;
      }
    }
  }
  .guarantees {
    @extend .icon;
    position: relative;
    box-sizing: border-box;
    height: 460px;
    padding: 75px 0 235px;
    text-align: center;
    &:before {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 51px;
      font-size: 150px;
      color: #ffc107;
      content: "\45";
    }
    .guarantees-content {
      max-width: 810px;
    }
  }
  .under-control {
    height: 410px;
    padding: 75px 0;
    background: #f3f3f3;
    box-sizing: border-box;
    .under-control-content {
      position: relative;
      max-width: 620px;
      margin: auto;
      padding: 60px 400px 60px 0;
      background: #f3f3f3 url('../img/information-buyers-laptop.svg') right center no-repeat;
      @media only screen and (max-width: 1150px) {
        height: 100px;
        padding: 40px 400px 60px 40px;
        background-position-x: 97%;
      }
    }
  }
  .individual-terms {
    box-sizing: border-box;
    height: 410px;
    padding: 75px 0;
    text-align: center;
  } //
  // QUESTIONS
  //
  .questions {
    padding: 75px 0;
  }
  .questions-carousel {
    color: $txt-black;
    .carousel-item-content {
      line-height: 25px;
    }
  }
  .ask-question {
    box-sizing: border-box;
    height: 302px;
    padding: 80px 0 60px;
    color: #fff;
    background: #a1887f;
    .ask-question-text {
      margin-top: 0;
      margin-bottom: 37px;
      font-size: 21px;
      line-height: 1.67;
      font-weight: 500;
    }
  } //
  // KNOWLEDGE
  //
  .ideas {
    padding: 74px 0;
    text-align: center;
    .title {
      color: #383838;
    }
    &-items {
      display: flex;
      padding: 75px 0 30px;
    }
    &-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 25%;
      &-icon {
        position: relative;
        width: 130px;
        height: 130px;
        &.icon-shareideas {
          color: #ffc107;
          font-size: 111px;
        }
        &.icon-askquestion {
          top: 7px;
          color: #4285f4;
          font-size: 112px;
        }
        &.icon-organization {
          top: 11px;
          color: #8bc34a;
          font-size: 96px;
        }
        &.icon-track {
          top: 5px;
          color: #e91e63;
          font-size: 120px;
        }
      }
      &-txt {
        width: 83%;
        padding: 20px 28px;
        color: #000;
        font-size: 16px;
        line-height: 1.56;
        text-align: center;
      }
    }
  }
  .share {
    padding: 74px 0;
    color: #fff;
    background-color: #617680;
    .title-pri {
      color: #fff;
    }
    &-txt {
      padding: 30px 0 50px;
      font-size: 16px;
      line-height: 1.56;
      text-align: center;
      a {
        color: #fff;
        font-weight: 700;
      }
    }
    &-icons {
      text-align: center;
      .icon {
        display: inline-block;
        width: 70px;
        height: 70px;
        font-size: 70px;
        margin: 0 50px;
      }
    }
  }
  .solve {
    box-sizing: border-box;
    min-height: 370px;
    padding: 78px 0;
    color: #383838;
    background-image: url(../img/information-solve-puzzle.svg);
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: bottom center;
    text-align: center;
    &-txt {
      padding: 30px 0;
    }
  }
  .teams {
    padding: 74px 0;
    color: #fff;
    background-color: #8bc34a;
    .title-pri {
      color: #fff;
    }
    &-body {
      display: flex;
      justify-content: space-around;
      align-items: center;
      margin-top: 50px;
      .icon-organization-long {
        position: relative;
        top: 40px;
        font-size: 323px;
        line-height: 133px;
        pointer-events: none;
        &:before {
          line-height: 0;
        }
      }
    }
    &-txt {
      width: 37%;
      line-height: 1.56;
    }
  }
  .look {
    padding: 74px 0 25px;
    color: #383838;
    text-align: center;
    &-txt {
      margin: 30px 0 18px;
      line-height: 1.56;
    }
    .icon-track {
      font-size: 118px;
      color: #e91e63;
    }
  }
  .gone {
    padding: 85px 0 60px;
    color: #fff;
    background-color: #a1887f;
    text-align: center;
    &-title {
      margin-bottom: 40px;
      font-size: 21px;
      font-weight: 500;
    }
  }
}
