.footer {
	background: $footerColor;
	color: #383838;
	font-size: 17px;
	text-align: center;
	.wrapper {
		padding: 50px 55px;
		position: relative;
	}
	a,
	a:hover {
		color: #383838;
	}
	&-menu {
		list-style: none;
		margin: 0 0 20px;
		font-weight: 500;
		padding: 0;
		&-item {
			display: inline-block;
			margin: 0 10px;
		}
	}
	&-support-link {
		font-weight: 500;
	}
	&-social {
		margin-top: 48px;
		.footer &-link {
			display: inline-block;
			margin: 0 5px;
      padding-top: 2px;
			font-size: 42px;
			line-height: 42px;
			width: 42px;
			height: 44px;
			overflow: hidden;
			color: #5a5a5a;
			&:hover {
				color: #777;
				text-decoration: none;
			}
		}
	}
	&-copyright {
		text-shadow: 0 0 0 #383838;
		position: absolute;
		left: 55px;
		bottom: 52px;
	}
}
