.post {
	&-grid {
		display: flex;
		margin: -18px;
		.post-list {
			position: relative;
			box-sizing: border-box;
			width: 50%;
			border: 18px solid transparent;
		}
	}
	&-list {
		margin: 0;
		padding: 0;
		list-style: none;
		&-item {
			position: relative;
			margin-top: 36px;
			padding: 20px;
			background: #f3f3f3;
			&--half {
				margin-top: 0;
				margin-bottom: 40px;
			}

			&:first-child {
				margin-top: 0;
			}
		}
	}
	&-header {
		position: relative;
		padding-left: 140px;
		min-height: 100px;

		a {
			color: inherit;
		}
	}
	&-image {
		position: absolute;
		left: 0;
		width: 100px;
		height: 100px;
	}
	&-controls {
		float: right;
		margin-top: 7px;
		text-align: right;
		span {
			vertical-align: middle;
		}
		a {
			margin-left: 15px;
			&:hover {
				color: $linkColor;
			}
		}
		.icon {
			span {
				display: none;
			}
		}
	}
	&-remove {
		font-size: 13px;
		a,
		a:hover {
			color: #bf1b39;
		}
	}
	&-remove-cancel {
		display: none;
		a {
			color: #0f9d58;
		}
	}
	&-new {
		font-size: 18px;
		font-weight: 500;
		color: #ff5722;
	}
	&-author {
		font-size: 25px;
		line-height: 1.1;
		font-weight: 500;
		word-break: break-word;
	}
	&-date {
		font-size: 14px;
		line-height: 1.4;
		color: #7f8c8d;
	}
	&-name {
		margin-top: 15px;
		padding-left: 0;
		font-size: 18px;
		font-weight: 400;
		word-break: break-word;
		&--new {
			@media #{$desktop-up} {
				margin-top: -40px;
				padding-left: 140px;
			}
		}
		a {
      position: relative;
			color: inherit;
		}
	}
	&-body {
		margin-top: 20px;
		line-height: 1.4;
		img {
			max-width:100%;
		}
	}
	&-footer {
		margin-top: 30px;
	}

	&-removed,
	&-remove-reasons {
		display: none;
		padding: 30px 0;
	}
	&-remove-reasons {
		p {
			font-weight: 500;
			margin-bottom: 20px;
		}
		label {
			display: block;
			margin: -10px 0 10px;
		}
		input[type="radio"] {
			margin-right: 8px;
			vertical-align: middle;
		}
		button {
			margin-top: 4px;
			padding: 0;
			color: #0f9d58;
			background-color: transparent; 
			border: 0 none;
			cursor: pointer;
			&:hover {
				text-decoration: underline;
			}
		}
	}
	&-removed {
		color: #ccc;
	}
}

// create post popup
#create-post-form {
	margin: 30px auto;
	padding: 40px 50px;
	color: #383838;

  .touch & {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    @media screen and (min-height: 700px) {
      top: 50%;
      transform: translate(-50%, -50%);
      margin: 0;
    }
  }

	.popup-title {
		margin-bottom: 40px;
	}
	.field {
		margin-top: 40px;
		border: none;
		border-radius: 0;
		height: auto;
		&:first-child {
			margin-top: 0;
		}
		&-label {
			font-size: 24px;
			font-weight: 500;
		}
		&-body {
			margin-top: 15px;
		}
		&-input {
			&-text {
				border: 1px solid #dcdcdc;
				border-radius: 7px;
				width: 700px;
				textarea {
					resize: none;
					height: 100px;
					padding: 15px;
				}
			}
			&-tag {
				display: block;
				height: 48px;
				background: none;
				border: 0;
				padding: 0 15px;
				font-weight: 400;
				font-size: 18px;
				line-height: 1.3;
				box-sizing: border-box;
				width: 100%;
				outline-color: #1abc9c;
				&:focus::placeholder {
					opacity: 0;
				}
			}
		}
	}
	.redactor {
		&-box {
			margin: 0;
		}
		&-editor {
			min-height: calc(100vh - 555px);
      .touch & {
        min-height: 400px;
      }
		}
	}
	.buttons {
		margin-top: 40px;
		text-align: right;
		.button {
			margin-left: 15px;
		}
	}
}