// Variables
// Declare any of these variables before importing this SCSS file to easily override defaults
// Variables are namespaced with the pd (pikaday) prefix

// Colours
$pd-text-color: #333 !default;
$pd-title-color: #383838 !default;
$pd-title-bg: #fff !default;
$pd-picker-bg: #fff !default;
$pd-picker-border: #ccc !default;
$pd-picker-border-bottom: #bbb !default;
$pd-picker-shadow: rgba(0,0,0,.5) !default;
$pd-th-color: #747474 !default;
$pd-day-color: #4c4c4c !default;
$pd-day-bg: #fff !default;
$pd-day-hover-color: #fff !default;
$pd-day-hover-bg: #ff5722 !default;
$pd-day-today-color: #ff5722 !default;
$pd-day-selected-color: #fff !default;
$pd-day-selected-bg: #ff5722 !default;
$pd-day-disabled-color: #999 !default;
$pd-week-color: #999 !default;

// Font
$pd-font-family: "Helvetica Neue", Helvetica, Arial, sans-serif !default;


.pika-single {
    padding: 10px;
    z-index: 9999;
    display: block;
    position: relative;
    color: $pd-text-color;
    background: $pd-picker-bg;
    border-radius: 7px;

    &.is-hidden {
        display: none;
    }

    &.is-bound {
        position: absolute;
        box-shadow: 0 0 2px 0 $pd-picker-shadow;
    }
}

// clear child float (pika-lendar), using the famous micro clearfix hack
// http://nicolasgallagher.com/micro-clearfix-hack/
.pika-single {
    margin-top: 10px;
    *zoom: 1;

    &:before,
    &:after {
        content: " ";
        display: table;
    }

    &:after { clear: both }
}

.pika-lendar {
    float: left;
    width: 242px;
    margin: 8px;
}

.pika-title {
    position: relative;
    text-align: center;

    select {
        cursor: pointer;
        position: absolute;
        z-index: 9998;
        margin: 0;
        left: 0;
        top: 5px;
        filter: alpha(opacity=0);
        opacity: 0;
    }
}

.pika-label {
    display: inline-block;
    *display: inline;
    position: relative;
    z-index: 9999;
    overflow: hidden;
    margin: 0;
    padding: 5px 3px;
    font-size: 18px;
    line-height: 20px;
    color: $pd-title-color;
    background-color: $pd-title-bg;
}

.pika-prev,
.pika-next {
    display: block;
    cursor: pointer;
    position: relative;
    width: 32px;
    height: 32px;
    color: transparent;
    outline: none;
    border: 0;
    padding: 0;
    background-color: #fff;
    text-indent: 20px; // hide text using text-indent trick, using width value (it's enough)
    white-space: nowrap;
    overflow: hidden;
    opacity: .5;
    *position: absolute;
    *top: 0;

    &:hover {
        opacity: 1;
    }

    &.is-disabled {
        cursor: default;
        opacity: .2;
    }
}

.pika-prev,
.is-rtl .pika-next,
.pika-next,
.is-rtl .pika-prev {
    float: left;

    &:before,
    &:after {
        display: block;
        position: absolute;
        content: "";
        width: 8px;
        height: 2px;
        top: 50%;
        left: 50%;
        background-color: #000;
        margin-left: -4px;
    }

    &:before {
        margin-top: -2px;
    }

    &:after {
        margin-top: 2px;
    }
}

.pika-prev,
.is-rtl .pika-next {
    float: left;

    &:before {
        transform: rotate(-45deg);
    }

    &:after {
        transform: rotate(45deg);
    }

    *left: 0;
}

.pika-next,
.is-rtl .pika-prev {
    float: right;

    &:before {
        transform: rotate(45deg);
    }

    &:after {
        transform: rotate(-45deg);
    }

    *right: 0;
}

.pika-select {
    display: inline-block;
    *display: inline;
}

.pika-table {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    border: 0;

    th,
    td {
        width: 14.285714285714286%;
        padding: 0;
    }

    th {
        padding-bottom: 12px;
        color: $pd-th-color;
        font-size: 15px;
        line-height: 38px;
        font-weight: 500;
        text-align: center;
    }

    abbr {
        border-bottom: none;
        cursor: help;
        text-decoration: none;
    }
}

.pika-button {
    cursor: pointer;
    display: block;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    outline: none;
    border: 0;
    margin: 0;
    width: 100%;
    padding: 5px;
    color: $pd-day-color;
    font-size: 16px;
    line-height: 26px;
    text-align: center;
    background: $pd-day-bg;

    .is-today & {
        color: $pd-day-today-color;
        font-weight: bold;
    }

    .is-selected & {
        color: $pd-day-selected-color;
        font-weight: bold;
        background: $pd-day-selected-bg;
    }

    .is-disabled &,
    .is-outside-current-month & {
        pointer-events: none;
        cursor: default;
        color: $pd-day-disabled-color;
        opacity: .3;
    }

    &:hover {
        color: $pd-day-hover-color;
        background: $pd-day-hover-bg;
    }
}

.pika-week {
    font-size: 11px;
    color: $pd-week-color;
}

.is-inrange .pika-button {
    background: #D5E9F7;
}

.is-startrange .pika-button {
    color: #fff;
    background: #6CB31D;
    box-shadow: none;
    border-radius: 3px;
}

.is-endrange .pika-button {
    color: #fff;
    background: #33aaff;
    box-shadow: none;
    border-radius: 3px;
}
