.tooltip {
	display: inline-block;
	position: relative;
	top: 1px;
	left: 3px;
	font-size: 20px;
	vertical-align: middle;
	cursor: help;
	& + .tooltip-popup {
		display: none;
		position: absolute;
		background: #fff;
		box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.5);
		border-radius: 7px;
		padding: 10px 15px;
		color: #7f8c8d;
		font-weight: 400;
		text-align: left;
		min-width: 300px;
		font-size: 16px;
		z-index: 98;
		margin-left: 15px;
		top: -15px;
		left: 100%;
	}
	& + .tooltip-popup:after,
	& + .tooltip-popup:before {
		right: 100%;
		top: 25px;
		border: solid transparent;
		content: ' ';
		height: 0;
		width: 0;
		position: absolute;
		pointer-events: none;
	}

	& + .tooltip-popup:after {
		border-color: rgba(136, 183, 213, 0);
		border-right-color: #fff;
		border-width: 5px;
		margin-top: -5px;
	}
	& + .tooltip-popup:before {
		border-color: rgba(194, 225, 245, 0);
		border-right-color: rgba(0, 0, 0, 0.5);
		border-width: 6px;
		margin-top: -6px;
	}
	&:hover + .tooltip-popup {display: block;}
}