/* Simple */
.simple {
    min-height: 768px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-content: stretch;
    color: #383838;

    & > .wrapper {
        flex: 1 0 auto;
        padding: 100px 70px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
    }

    &.error > .wrapper {
      padding: 243px 70px 218px;
    }

    h1 {
        text-align: center;
        font-size: 32px;
        font-weight: 500;
        margin: 0 0 50px;
    }

    h1 + p {
        margin: -20px 0 34px;
        line-height: 1.56;
    }

    &-content {
        text-align: center;
    }

    .footer {
        flex: 0 0 auto;
    }

    .bit-txt {
        color: #0f9d58;
        font-size: 100px;
        line-height: 102px;
    }
}