.order {

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__wrapper {
    margin-top: 20px;
    padding: 25px;
    background-color: #f3f3f3;
    border-radius: 8px;
  }

  &__body {
    max-height: 400px;
    margin-bottom: 20px;
    overflow-y: auto;
  }

  &__item {
    display: flex;
    padding: 19px;
    background-color: #fff;

    & + & {
      margin-top: 20px;
    }
  }

  &__img {
    flex-shrink: 0;
    display: block;
    width: 120px;
    height: 114px;
    margin-right: 20px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }

  &__details {
    margin-top: 10px;
    color: #4c4c4c;
    font-size: 16px;

    tr > td:first-child {
      font-weight: 500;
      vertical-align: top;
    }

    tr > td:last-child {
      padding-left: 16px;
    }

    td {
      padding-bottom: 6px;
    }
  }

  &__title {
    color: #383838;
    font-size: 20px;
    font-weight: 500;

    &:hover {
      color: #383838;
      text-decoration: none;
    }
  }

  &__comment {
    display: block;
    font-size: 17px;
    text-align: center;

    &.active {
      display: none;
    }
  }

  &__form {
    display: none;

    &.open {
      display: block;
    }
  }

  &__footer {
    margin-top: 20px;
    padding-top: 20px;
    font-size: 20px;
    border-top: solid 2px #dedede;
  }

  &__total {
    width: 100%;

    tr > td:first-child {
      font-weight: 500;
    }

    tr > td:last-child {
      text-align: right;
    }

    tr:not(:last-child) > td {
      padding-bottom: 6px;
    }

    tr:last-child > td {
      color: #0f9d58;
      font-size: 24px;
      font-weight: 500;
    }
  }

}
