.selectize-input {
  .tools-search & {
    display: flex;
    align-items: center;
    height: 38px;
    min-height: 38px;
    padding: 0 40px 0 10px;
    background: transparent;
    text-align: left;
    border: 1px solid rgba(255, 255, 255, 0.25);
    box-sizing: border-box;
    cursor: text;
    .bright &,
    .shop-account & {
      border-color: #d3d3d3;
    }
  }
}
.selectize-control.single .selectize-input:after {
  .tools-search & {
    display: none;
  }
}
.selectize-control.multi .selectize-input.has-items {
  .tools-search & {
    padding: 0 40px 0 10px;
  }
}
.selectize-input.focus {
  .tools-search & {
    background-color: #fff;
    box-shadow: none;
  }
}
.selectize-dropdown,
.selectize-input,
.selectize-input input {
  .tools-search & {
    color: #fff;
    font-size: 15px;
    line-height: normal;
    .bright & {
      color: #4c4c4c;
    }
  }
}
.selectize-input,
.selectize-control.single .selectize-input.input-active {
  .tools-search & {
    display: flex;
  }
}
.selectize-control.single .selectize-input > div,
.selectize-control.multi .selectize-input > div {
  .tools-search & {
    position: relative;
    vertical-align: middle;
    margin: 0 4px 0 0;
    padding: 0 6px;
    color: #fff;
    line-height: 24px;
    white-space: nowrap;
    border: 0 none;
    border-radius: 4px;
    .bright &,
    .shop-account & {
      color: #303030;
    }
  }
}
.selectize-control.single .selectize-input.focus > div,
.selectize-control.multi .selectize-input.focus > div {
  .tools-search & {
    color: #303030;
  }
}
.selectize-input > input::placeholder {
  .tools-search & {
    color: #fff;
    opacity: 1;
    .bright &,
    .shop-account & {
      color: #303030;
    }
  }
}
.selectize-input > input:focus {
  .tools-search & {
    color: #4c4c4c;
  }
}
.selectize-dropdown {
  .tools-search & {
    width: 360px !important;
    height: auto;
    margin: 7px 0 0 0;
    padding: 4px 1px 4px 11px;
    color: #4c4c4c;
    line-height: 36px;
    text-align: left;
    border: 0 none;
    border-radius: 7px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  }
}
.selectize-input.dropdown-active {
  .tools-search & {
    border-radius: 7px;
  }
}
.selectize-dropdown-content {
  .tools-search & {
    max-height: 400px;
  }
}
.selectize-dropdown .optgroup-header {
  .tools-search & {
    font-weight: 500;
  }
}
.selectize-dropdown.plugin-optgroup_columns .optgroup {
  .tools-search & {
    width: 100% !important;
    float: none;
    border: 0 none;
  }
}
.selectize-dropdown [data-selectable],
.selectize-dropdown .optgroup-header {
  .tools-search & {
    padding: 0;
  }
}
.selectize-dropdown .active {
  .tools-search & {
    background-color: transparent;
  }
}
