@media only screen and (max-width: 1408px) {
    .main {
        .header {
            &-title {
                font-size: 48px;
                margin: 0 0 20px;
                span {
                    display: block;
                }
            }
            &-subtitle {
                font-size: 20px;
            }
        }
    }
    .pseudo-popup-close {
        right: 22px;
    }

    .popup-add-collection {
        margin-left: 70px;
        margin-right: 70px;
    }

    .shop-account .content-part .shop-account-recommends-empty.wrapper {
        padding-bottom: 16%;
    }
}