.header {
  &-img {
    position: relative;
    width: 100%;
    color: #fff;
    img {
      display: block;
      width: 100%;
      height: auto;
    }
  }
  &-img-content {
    box-sizing: border-box;
    position: absolute;
    width: 100%;
    min-width: 1000px;
    max-width: 1408px;
    bottom: 0;
    left: 50%;
    padding: 50px 70px;
    transform: translateX(-50%);
    &.center {
      top: 50%;
      bottom: auto;
      transform: translate(-50%, -50%);
      h1 {
        font-size: 40px;
        text-align: center;
      }
    }
  }
}
