html,
body {
	margin: 0;
	padding: 0;
	height: 100%;
}
body {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	position: relative;
	font: 400 100%/1.2 $fontRoboto;
	background: $footerColor;
	color: $textColor;
	min-width: 1000px;
	overflow: auto;
	text-size-adjust: 100%;
}
*, *:before, *:after {
  -webkit-tap-highlight-color: transparent;
}
p {
	margin: 1em 0;
}
input,
textarea,
select,
button {
	font: inherit;
}
input::-ms-clear {
	display: none;
}
::selection {
	background: #1abc9c;
	color: #fff;
}
.wrapper {
	box-sizing: border-box;
	min-width: 1000px;
	max-width: 1408px;
	padding-left: 30px;
	padding-right: 30px;
	margin: 0 auto;
}
a {
	color: $linkColor;
	text-decoration: none;
	outline: none;
	&.attention,
	&.attention:hover {
		color: $linkAttention;
		font-size: 17px;
	}
}
a:hover {
	color: $linkColor;
	text-decoration: underline;
}
picture img {
	display: block;
	width: 100%;
}
.pull-right {
	float: right;
}
.pull-left {
	float: left;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}
.text-center {
	text-align: center;
}
input:focus::placeholder {
	opacity: 0;
}
.hidden {
	display: none !important;
}
.clearfix {
	*zoom: 1;
	&:before,
	&:after {display: table; line-height: 0; content: ' ';}
	&:after {clear: both;}
}
.hr {
	position: relative;
	text-align: center;
	margin: 0 0 20px;
	&::before {
		content: '';
		position: absolute;
		left: 0;
		top: 50%;
		width: 100%;
		height: 2px;
		background: #ededed;
	}
	span {
		color: #383838;
		background: #fff;
		padding: 0 10px;
		position: relative;
		font-weight: 400;
	}
}

.hr-sec {
	display: block;
	width: 100%;
	height: 2px;
	margin: 50px 0;
	background: #dedede;
	border: 0 none;
	&.top-10 {
		margin-top: 10px;
	}
}

.title-pri {
  color: $txt-black;
	font-size: 32px;
	font-weight: 500;
	text-align: center;
}

textarea {
	overflow: auto;
}

.icn {
	display: inline-block;
	width: 16px; height: 16px; vertical-align: middle;
	&-help {
		border: 2px solid #0f9d58;
		border-radius: 10px;
		text-align: center;
		font-style: normal;
		color: #0f9d58;line-height: 18px;
	}
}
.errorlist {
	margin: 20px 0;
    list-style-type: none;
    li {
		line-height: 28px;
      	color: #cd0a0a;
    }
}
