.review {
	&-list {
		margin: 0;
		padding: 0;
		list-style: none;
	}
	&-item {
		margin-top: 50px;
		padding: 50px 0 0 140px;
		border-top: 2px solid #dedede;
		&:first-child {
			padding-top: 0;
			margin-top: 0;
			border: 0;
			.review-answers & {
				padding-top: 50px;
			}
		}
		.review-answers & {
			border: 0;
			margin-top: 0;
		}
	}
	&-body {
		position: relative;
	}
	&-image {
		position: absolute;
		left: -140px;
		top: 0;
		width: 100px;
		height: 100px;
	}
	&-author {
		margin-right: 25px;
		font-size: 25px;
		font-weight: 500;
        word-break: break-word;
		a {
			color: inherit;
		}
	}
	&-date {
		color: #7f8c8d;
		font-size: 14px;
		font-weight: 400;
		line-height: 1.4;
	}
	&-text {
		color: #7f8c8d;
		margin: 15px 0 0;
		p {
			margin: 15px 0 0;
			&:first-child {
				margin: 0;
			}
		}
	}
	&-answer {
		margin: 15px 0 0;
		font-size: 17px;
		font-weight: 400;
		form {
			display: none;
			margin: 0 0 10px;
			padding: 0;
		}
		.textarea {
			border: 1px solid #d3d3d3;
			border-radius: 7px;
			padding: 0;
			textarea {
				border: 0;
				width: 100%;
				padding: 7px;
				box-sizing: border-box;
				display: block;
				background: none;
				font-size: 15px;
				outline-color: #1abc9c;
				resize: none;
			}
		}
	}
	&-remove {
		position: absolute;
		right: 0;
		top: 5px;
		&-link.icon {
      padding: 1px;
			display: block;
			font-size: 16px;
			height: 18px;
			width: 18px;
			letter-spacing: 2px;
			padding-left: 1px;
			box-sizing: border-box;
			overflow: hidden;
			color: #bf1b39;
		}
		&-reason,
		&-done {
			display: none;
			position: relative;
			min-height: 100px;
			& .review-image {
				opacity: .3;
			}
			& .review-author,
			& .review-date,
			& .review-text {
				color: #ccc;
			}
			a {
				font-weight: 400;
			}
		}
		&-reason {
			form {
				margin: 0;
			}
			b {
				display: block;
				margin-top: 15px;
			}
			label {
				display: block;
				margin: -10px 0 10px;
				&:first-child {
					margin-top: 10px;
				}
			}
		}
		&-done {
			color: #ccc;
		}
	}
}
