.tags {
	margin-top: 20px;
	&-item {
		padding: 15px 14px;
    line-height: 1.22;
    color: #4c4c4c;
    font-size: 18px;
    border-radius: 8px;
    background-color: #f3f3f3;
    font-weight: 400;
    display: inline-block;
		&:not(:first-child) {
			margin-left: 16px;
		}
	}
	&-remove {
		text-decoration: none;
		color: #bf1b39;
		font-size: 13px;
		margin-left: 10px;
		&:hover {
			color: #bf1b39;
		}
	}
}