.select2-container {
  &--tags {
    padding-right: 35px;
    height: 50px;
    .select2-selection--multiple {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      height: 48px;
      border-radius: 7px;
      .select2-selection__rendered {
        margin: 0;
        width: 95%;
        padding-left: 0;
        border-left: 15px solid transparent;
        display: inline-block;
        text-align: left;
        .select2-search--inline {
          float: none;
          display: inline-block;
          line-height: 46px;
          width: 100%;
          .select2-search__field {
            height: 46px;
            margin-top: 0;
            font-size: 18px;
            cursor: pointer;
            &:focus::placeholder {
              opacity: 1;
            }
          }
        }
        .select2-selection__choice {
          display: inline;
          line-height: 46px;
          color: #4c4c4c;
          font-size: 18px;
          font-weight: 400;
          position: relative;
          &+.select2-selection__choice {
            padding-left: 10px;
            &:before {
              content: ',';
              position: relative;
              left: -10px;
            }
          }
          &+.select2-search--inline {
            width: auto;
            .select2-search__field {
              width: auto;
            }
          }
          .select2-selection__choice__remove {
            display: none;
          }
        }
      }
      &:after {
        border-color: #000 transparent transparent transparent;
        border-style: solid;
        border-width: 8px 8px 0;
        height: 0;
        width: 0;
        content: ' ';
        position: absolute;
        top: 19px;
        right: 12px;
      }
    }
    .select2-dropdown {
      max-height: 260px;
      overflow: auto;
      border-radius: 4px;
      border: 1px solid #aaa;
      top: 7px;
      .select2-results {
        .select2-results__option {
          border-top: solid 2px #d7d7d7;
          padding: 0 6px 0 52px;
          font-weight: 400;
          position: relative;
          line-height: 2.83;
          color: #4c4c4c;
          font-size: 18px;
          &:first-child {
            border-top: transparent;
          }
          &:before {
            display: block;
            width: 22px;
            height: 22px;
            border-radius: 2px;
            border: solid 1px #d5d5d5;
            content: ' ';
            position: absolute;
            top: 15px;
            left: 10px;
          }
          &[aria-selected="true"] {
            &:before {
              display: block;
              width: 22px;
              height: 22px;
              border-radius: 2px;
              border: solid 1px #0f9d58;
              background: #0f9d58;
              content: ' ';
              position: absolute;
              top: 15px;
              left: 10px;
            }
            &:after {
              content: '';
              border: 2px solid #fff;
              border-width: 0 0 4px 4px;
              position: absolute;
              width: 10px;
              height: 6px;
              -webkit-transform: rotate(-45deg);
              transform: rotate(-45deg);
              left: 15px;
              top: 20px;
            }
          }
        }
      }
    }
  }
}

.select2-container {
  &--primary {
    position: relative;
    .select2-selection {
      height: 100%;
      &--single {
        height: 100%;
        outline: none;
        .select2-selection__rendered {
          padding-left: 15px;
          color: #4c4c4c;
          font-size: 18px;
          line-height: 47px;
        }
        .select2-selection__placeholder {
          color: #7f8c8d;
        }
      }
      &__arrow {
        position: absolute;
        top: 50%;
        right: 16px;
        transform: translateY(-50%);
        b {
          display: block;
          width: 0;
          height: 0;
          border-width: 8px 8px 0;
          border-style: solid;
          border-color: #000 transparent transparent transparent;
        }
      }
    }
    .select2-dropdown {
      margin-top: 8px;
      color: #000;
      background: #fff;
      border: 0;
      font-weight: 400;
      overflow: hidden;
      border-radius: 7px;
      box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
    }
    .select2-results {
      &__option {
        padding: 10px 15px;
        line-height: 18px;
        &--highlighted {
          background: #ff5722;
          color: #fff;
        }
      }
      &__option[aria-disabled] {
        height: 2px;
        padding: 0;
        color: transparent;
        background: #dedede;
        pointer-events: none;
      }
    }
  }
}

.select2-container {
  &.select2-container--open {
    margin-top: 5px;
    top: -5px;
  }
  &--check {
    width: 300px !important;
    height: 45px;
    border-radius: 7px;
    background-color: transparent;
    border: 1px solid rgba(255, 255, 255, .25);
    @media only screen and (max-width: 1150px) {
      width: 240px !important;
      height: 35px;
    }
    .select2-search--inline {
      float: none;
      display: inline-block;
      width: 100%;
      line-height: 44px;
      @media only screen and (max-width: 1150px) {
        line-height: 33px;
      }
      .select2-search__field {
        height: 44px;
        margin: 0;
        background-color: transparent;
        font-size: 18px;
        outline: 0 !important;
        @media only screen and (max-width: 1150px) {
          height: 33px;
          font-size: 15px !important;
        }
        &::placeholder {
          color: #fff;
          font-weight: 400;
          opacity: 1;
        }
      }
    }
    .select2-selection__choice {
      position: relative; //display: inline;
      display: none;
      color: #fff;
      line-height: 43px;
      font-size: 18px;
      font-weight: 400;
      @media only screen and (max-width: 1150px) {
        font-size: 15px;
        line-height: 33px;
      }
      &+.select2-selection__choice {
        padding-left: 10px;
        &:before {
          content: ',';
          position: relative;
          left: -10px;
        }
      }
      &+.select2-search--inline {
        display: none;
        width: auto;
        .select2-search__field {
          width: auto;
        }
      }
      .select2-selection__choice__remove {
        display: none;
      }
    }
    &.select2-container--open {
      background-color: #fff;
      .select2-selection--multiple:before {
        color: #4c4c4c;
      }
      .select2-selection--multiple:after {
        border-color: #4c4c4c transparent transparent transparent;
      }
      .select2-search__field {
        color: #4c4c4c;
        font-weight: 400;
      }
      .select2-selection__choice {
        color: #4c4c4c;
      }
    }
    .select2-selection--multiple {
      height: 100%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      border-radius: 7px;
      .select2-selection__rendered {
        margin: 0;
        width: 90%;
        padding-left: 0;
        border-left: 15px solid transparent;
        display: inline-block;
        text-align: left;
      }
      &:before {
        position: absolute;
        content: 'Показывать в новостях';
        top: 12px;
        left: 15px;
        color: #fff;
        font-size: 18px;
      }
      &:after {
        border-color: #fff transparent transparent transparent;
        border-style: solid;
        border-width: 8px 8px 0;
        height: 0;
        width: 0;
        content: ' ';
        position: absolute;
        top: 19px;
        right: 12px;
        @media only screen and (max-width: 1150px) {
          top: 14px;
        }
      }
    }
    .select2-dropdown {
      width: 298px !important;
      background-color: #fff;
      border: 0 none;
      border-radius: 7px;
      box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.5);
      @media only screen and (max-width: 1150px) {
        width: 238px !important;
      }
      .select2-results {
        .select2-results__group {
          padding-left: 10px;
          font-weight: 500;
        }
        .select2-results__group+ul {
          margin-top: -10px;
        }
        .select2-results__option {
          border-top: solid 2px #d7d7d7;
          padding: 0 0 0 61px;
          font-weight: 400;
          position: relative;
          line-height: 2.3;
          color: #4c4c4c;
          font-size: 18px;
          &:first-child {
            border-top: transparent;
          }
          &:before {
            display: block;
            width: 22px;
            height: 22px;
            border-radius: 2px;
            border: solid 1px #d5d5d5;
            content: ' ';
            position: absolute;
            top: 10px;
            left: 20px;
          }
          &[role="group"] {
            padding-left: 0;
            &:before {
              display: none !important;
            }
          }
          &[aria-selected="true"] {
            &:before {
              display: block;
              width: 22px;
              height: 22px;
              border-radius: 2px;
              border: solid 1px #0f9d58;
              background: #0f9d58;
              content: ' ';
              position: absolute;
              top: 10px;
              left: 20px;
            }
            &:after {
              content: '';
              border: 2px solid #fff;
              border-width: 0 0 3px 3px;
              position: absolute;
              width: 13px;
              height: 7px;
              transform: rotate(-45deg);
              left: 25px;
              top: 16px;
            }
          }
        }
      }
    }
  }
}

// override selectize arrow
.selectize-control.single .selectize-input.dropdown-active:after {
  margin-top: -3px;
  border-width: 8px 8px 0;
  border-color: #000 transparent transparent transparent;
}

