.compilation-name {
    position: absolute;
    display: block;
    bottom: 12px;
    left: 0;
    right: 0;
    padding: 20px;
    line-height: 29px;
    font-size: 25px;
    font-weight: 700;
    color: #fff;
    z-index: 2;

    &:hover {
        text-decoration: none;
    }

    &::before {
        position: absolute;
        content: "";
        display: block;
        bottom: 0;
        left: 0;
        right: 0;
        height: 130px;
        background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.25));
        z-index: -1;
    }
}