/* Shop */
.shop {
  padding-top: 75px;
  &.preview {
    padding-top: 0;
  }
  .brand {
    &-header {
      .brand-card {
        left: 280px;
      }
      .item-tools {
        top: 50px;
        right: 70px;
      }
      &._shop {
        .brand-card {
          left: 70px;
        }
      }
    }
  }
  &-creation {
    color: #383838;
    .header {
      background: url(../img/shop_creation.jpg);
      background-size: cover;
      background-position: 50% 50%;
      color: #fff;
      height: $page-header-height;
      box-sizing: border-box;
      text-align: center;
      position: relative;
      display: flex;
      align-items: center;
      &-title {
        margin: 0;
        font-size: 40px;
        line-height: 1.2;
        font-weight: 400;
        span {
          display: block;
        }
      }
    }
    .step {
      .wrapper {
        padding: 50px 70px;
      }
      &-title {
        text-align: center;
        font-size: 32px;
        font-weight: 500;
        color: #383838;
        width: 800px;
        margin: 0 auto 50px;
      }
      &.final {
        .step {
          &-title {
            font-size: 45px;
            font-weight: 700;
          }
          &-subtitle {
            line-height: 25px;
            font-weight: 400;
            width: 800px;
            margin: 0 auto 50px;
          }
        }
      }
      &-subtitle {
        color: #383838;
        margin: -20px 0 40px;
        font-weight: 400;
      }
      &.final {
        .wrapper {
          padding-top: 150px;
        }
        &-title {
          font-size: 45px;
          font-weight: 700;
          color: #383838;
          margin: 0 0 50px;
        }
        p {
          text-align: center;
          .button {
            width: 270px;
            box-sizing: border-box;
            text-align: center;
            margin: 0 25px;
            padding: 0;
          }
        }
      }
      .fieldset {
        margin: 0;
        padding: 0;
        border: 0;
      }
      .field {
        border-top: 2px solid #dedede;
        padding-top: 50px;
        margin-top: 50px;
        &:first-child {
          margin: 0;
          padding: 0;
          border: 0;
        }
        &-label {
          font-size: 24px;
          font-weight: 500;
        }
        &-body {
          margin-top: 15px;
          .inline {
            display: inline-block;
            margin-right: 20px;
            vertical-align: top;
            a {
              line-height: 50px;
              font-size: 17px;
              font-weight: 400;
            }
          }
          .country {
            width: 270px;
            select {
              width: 100%;
            }
          }
          .city {
            width: 330px;
            select {
              width: 100%;
            }
          }
          .zip {
            width: 200px;
          }
          .phone {
            width: 270px;
            position: relative;
          }
          .code {
            width: 115px;
          }
        }
        & p {
          margin: 15px 0 20px;
          font-weight: 400;
          color: #4c4c4c;
        }
        &-input {
          &-text {
            border: 1px solid #dcdcdc;
            border-radius: 7px;
            width: 700px;
            textarea {
              resize: none;
              height: 100px;
              padding: 15px;
            }
            &.field-input-text-large {
              width: 100%;
            }
          }
          &-tag {
            display: block;
            height: 48px;
            background: none;
            border: 0;
            padding: 0 15px;
            font-weight: 400;
            font-size: 18px;
            line-height: 1.3;
            box-sizing: border-box;
            width: 100%;
            outline-color: #1abc9c;
            &:focus::placeholder {
              opacity: 0;
            }
          }
        }
        &.required {
          .field-label::after {
            content: " *";
          }
        }
        &.sms-code {
          .button {
            min-width: 190px;
            box-sizing: border-box;
            padding: 0 15px;
            text-align: center;
            height: 50px;
            line-height: 50px;
          }
          .inline {
            b,
            span {
              display: block;
              font-size: 18px;
              font-weight: 400;
              line-height: 25px;
              a {
                line-height: 25px;
              }
            }
            small {
              display: block;
              line-height: 1.2;
              a {
                font-size: 100%;
              }
            }
          }
        }
        .tile-radio {
          &-input {
            display: none;
            &:checked+.tile-radio-label {
              background: #f3f3f3;
              &::before {
                border: solid 2px #0f9d58;
                background: #0f9d58;
              }
            }
          }
          &-label {
            height: 110px;
            display: flex;
            align-items: center;
            box-sizing: border-box;
            padding: 15px 15px 15px 80px;
            border-radius: 8px;
            font-weight: 400;
            @media only screen and (max-width: 1250px) {
              height: 140px;
            }
            &::before {
              content: "";
              position: absolute;
              top: 50%;
              transform: translate(0, -11px);
              left: 30px;
              width: 22px;
              height: 22px;
              box-sizing: border-box;
              border-radius: 50%;
              border: solid 2px #5a5a5a;
              background: #fff;
              cursor: pointer;
            }
            b {
              display: block;
              font-size: 18px;
              font-weight: 400;
              margin: 0 0 5px;
            }
          }
        }
        .select2-container--default {
          .select2-selection--multiple {
            overflow: hidden;
          }
          .select2-selection--multiple,
          .select2-selection--single {
            border: 1px solid #dcdcdc;
            font-size: 18px;
            box-sizing: border-box;
            height: 50px;
            width: 100%;
            .select2-selection__rendered {
              display: block;
              line-height: 48px;
              font-weight: 400;
            }
            .select2-selection__arrow {
              top: 29px;
              right: 12px;
            }
            ul {
              list-style: none;
            }
            .select2-selection__choice__remove {
              display: none;
            }
          }
        }
        .select2-search--inline .select2-search__field {
          margin-top: 0;
          overflow: hidden;
        }
        .select2-container--open .select2-search--inline .select2-search__field {
          opacity: 1;
          overflow: visible;
        }
      }
    }
    &-nav {
      border-top: 2px solid #dedede;
      margin-top: 70px;
      padding-top: 50px;
      overflow: hidden;
      .button {
        width: 220px;
        box-sizing: border-box;
        text-align: center;
        padding: 0 2em;
        &.lg {
          width: auto;
        }
      }
      .step.final & {
        margin-top: 150px;
      }
      &-right {
        text-align: right;
        .button {
          margin-left: 20px;
        }
      }
      &-settings {
        text-align: right;
        .button {
          display: inline-block;
          width: auto;
          margin-left: 20px;
        }
        .brand-remove {
          display: block;
          float: left;
          max-width: 36%;
          text-align: left;
          a {
            font-size: 17px;
            line-height: 1.18;
            color: #ff0000;
            font-weight: 400;
            text-decoration: none;
            &:hover {
              text-decoration: underline;
            }
          }
          span {
            display: block;
            font-weight: 400;
            margin-top: 5px;
          }
        }
      }
    }
  }
}
