.goods {
  &-item {
    .tile-list-item-state {
      position: absolute;
      top: 0;
      padding-top: 49%;
      z-index: 4;
    }
    &:hover .goods-info {
      bottom: 0;
    }
    .tile-list-item-type {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      height: 100%;
      width: 100%;
      text-align: center;
      color: #fff;
      font-size: 22px;
      font-weight: 500;
      .tile-list-item-info {
        position: absolute;
        height: auto;
        max-height: 110px;
        overflow: hidden;
        text-overflow: ellipsis;
        top: 0;
        left: 0;
        right: 0;
        margin-top: 55%;
        transform: translateY(-50%);
        padding-left: 10px;
        padding-right: 10px;
      }
      .button-action {
        position: absolute;
        left: 50%;
        top: 0;
        margin-top: 83%;
        transform: translateX(-50%);
      }
    }
    &.tile-list-item-removed {
      .compilation-name,
      .tile-list-item-type {
        display: none;
      }
    }
  }
  &-image {
    display: block;
    position: relative;
    width: 100%;
    margin: 0 0 12px;
    background-size: cover;
    overflow: hidden;
    &::before {
      content: '';
      @include posa(0, 0, 0, 0);
      background: #000;
      opacity: .1;
      transition: .4s ease opacity, .4s ease background-color;
      .goods-item:hover & {
        opacity: .3;
      }
      .tile-list .tile-list-item-removed & {
        background: rgb(168, 0, 0);
        opacity: .7;
      }
      .tile-list .tile-list-item-added & {
        background: $linkColor;
        opacity: .7;
      }
    }
    &-collection {
      z-index: 1;
    }
    &-empty {
      &::before {
        background-color: #8a9397;
        opacity: 1;
        .goods-item:hover & {
          background-color: darken(#8a9397, 10%);
          opacity: 1;
        }
      }
    }
  }
  .add-product-list &-image {
    padding-top: 100%;
  }
  &-link,
  &-link:hover {
    display: block;
    font-size: 18px;
    font-weight: 400;
    color: #383838;
  }
  &-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: 22px;
    color: #fff;
    font-size: 20px;
    font-style: normal;
    transform: translate(-50%, -50%);
    &::before {
      position: absolute;
      top: -70px;
      font-size: 68px;
    }
    @media only screen and (max-width: 1150px) {
      font-size: 18px;
      &::before {
        top: -56px;
        font-size: 56px;
      }
    }
    .tile-list-item-removed & {
      opacity: .2;
    }
  }
  &-name {
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    height: 1.2em;
    margin: 0 0 3px;
  }
  &-shop,
  &-shop:hover {
    position: relative;
    font-size: 18px;
    font-weight: 400;
    color: #7f8c8d;
    z-index: 1;
  }
  &-price {
    display: block;
    font-size: 24px;
    margin: 3px 0 0;
    font-weight: 500;
    color: #383838;
    .icon {
      font-size: 18px;
    }
  }
  &-tools {
    text-align: center;
  }
  &-info {
    background: #ff5722;
    color: #fff;
    padding: 18px 20px;
    position: absolute;
    left: 0;
    right: 0;
    bottom: -210px;
    height: 210px;
    font-size: 16px;
    box-sizing: border-box;
    overflow: hidden;
    transition: all .3s;
    @media only screen and (max-width: 1150px) {
      font-size: 12px;
    }
  }
}

