.subscriber {
	&-item {
		padding-bottom: 25px;
	}
	&-link {
		&,
		&:hover {
			display: block;
			color: #383838;
		}
	}
	&-name {
		font-size: 24px;
		font-weight: 500;
		line-height: 1.2;
		height: 1.2em;
		overflow: hidden;
		text-overflow: ellipsis;
		margin: 0 0 15px;
	}
	&-image {
		max-width: 100%;
		display: block;
		position: relative;
		margin: 0 0 15px;
		&::before {
			content: '';
			@include posa(0, 0, 0, 0);
			background: rgba(0, 0, 0, .15);
			transition: background ease .3s;
			.subscriber-item:hover & {
				background: rgba(0, 0, 0, .3);
			}
		}
	}
	&-items {
		display: flex;
		align-items: flex-start;
		flex-wrap: wrap;
		margin: -10px;
		padding: 0;
		list-style: none;
		&-one {
			width: 33.33%;
			box-sizing: border-box;
			border: 10px solid transparent;
		}
		&-image img {
			transition: all .2s;
			.subscriber-items-one:hover & {
				transform: scale(1.05);
			}
		}
		&-more {
			@include aspect-ratio(1, 1);
			display: block;
			background: #0f9d58;
			color: #fff;
			font-size: 14px;
			font-weight: 400;
			transition: background ease .3s;
			overflow: hidden;
			&:hover {
				color: #fff;
				text-decoration: none;
				background: #0d864b;
			}
		}
		&-content {
			margin-top: -1.2em;
			padding-top: 50%;
			text-align: center;
		}
		&-unit {
			display: block;
		}
	}
}