.carousel {
    $item-padding-tb: 32px;
    $item-padding-lr: 34px;
    position: relative;

    &.is-new {
        padding-left: 30px;
        &:before {
            position: absolute;
            display: block;
            content: '';
            top: 0;
            left: 0;
            width: 15px;
            height: 90px;
            background-color: #e91e63; 
        }
    }

    .carousel-item {
        position: relative;
        margin-bottom: 10px;
        background: #f3f3f3;
        border-radius: 8px;
        overflow: hidden;
    }

    .carousel-title {
        position: relative;
        height: 90px;
        padding-left: 109px;
        font-size: 20px;
        line-height: 90px;
        font-weight: 500;
        color: $linkColor;
        cursor: pointer;

        &:before {
            position: absolute;
            top: -2px;
            left: $item-padding-lr;
            font-size: 45px;
            content: '\002B';
        }

        &:hover .carousel-title-text { text-decoration: underline; }
    }

    .carousel-item.open .carousel-title:before { content: '\2212'; }
    .carousel-container { display: none; }

    .carousel-subtitle {
        padding: $item-padding-tb $item-padding-lr;
        font-size: 20px;
        font-weight: 500;
        cursor: pointer;
        border-top: 2px solid #dedede;

        &.active,
        &:hover { color: $linkColorAlt; }
    }

    .carousel-item-content {
        display: none;
        padding: 0 $item-padding-lr 18px $item-padding-lr;
        margin-top: -32px;

        &.open { display: block; }
    }

    hr {
        position: absolute;
        display: block;
        left: 0;
        width: 100%;
        height: 2px;
        margin: 0;
        background-color: #dedede;
        border: 0 none; 
    }
}