.brand-settings-history {
    position: relative;

    &-buttons {
        display: table;
        font-size: 0;
        position: absolute;
        right: 5px;
        top: 5px;
        z-index: 101;

        .button {
            margin: 0;
            margin-left: 5px;
            padding: 0 30px;
            &.yellow {
                position: absolute;
                right: 0;
            }
        }
    }
}