.comments {
    &-toggle {
        margin-bottom: 30px;
        .button {
            height: 55px;
            line-height: 54px;
            width: 100%;
            background: #ffc107;
			&:hover {
				background: darken(#ffc107, 5%);
			}
            &.active {
                background: #7f8c8d;
                &:hover {
                    background: darken(#7f8c8d, 5%);
                }
                span {
                    display: none;
                    &:nth-child(2) {
                        display: inline;
                    }
                }
            }
        }
        span {
            display: none;
            &:nth-child(1) {
                display: inline;
            }
        }
    }
	&-body {
        display: none;
        padding: 15px;
        max-height: 666px;
        overflow-y: auto; 
        background: #fff;
    }
    // rewrite review.scss rules
    .review {
        &-item {
            margin-top: 20px;
            padding: 20px 0 0 80px;
            &:first-child {
                padding-top: 0;
                margin-top: 0;
            }
        }
        &-answers {
            .review-item {
                margin-top: 0;
                &:first-child {
                    padding-top: 20px;
                }
            }
        }
        &-image {
            left: -80px;
            width: 65px;
            height: 65px;
        }
        &-remove-link.icon {
            width: 15px;
            font-size: 13px;
        }
        &-text {
            margin-top: 10px;
            line-height: 1.4;
        }
    }
    &-form {
        .field {
            margin: 30px 0 20px;
            &-input {
                &-text {
                    border: 1px solid #dcdcdc;
                    background: #fff;
                    border-radius: 7px;
                    width: 100%;
                    textarea {
                        resize: none;
                        height: 150px;
                        padding: 15px;
                    }
                }
                &-tag {
                    display: block;
                    height: 48px;
                    background: none;
                    border: 0;
                    padding: 0 15px;
                    font-weight: 400;
					font-size: 18px;
                    line-height: 1.3;
                    box-sizing: border-box;
                    width: 100%;
                    outline-color: #1abc9c;
                    &:focus::placeholder {
                        opacity: 0;
                    }
                }
            }
        }
        .button {
            min-width: 280px;
        }
    }
}

@media only screen and (max-width: 1150px) {
    .comments {
        .review-answers {
            margin-left: -80px;
        }
    }
}