.pseudo-popup {
    color: #383838;
    position: relative;
    background: $backgroundColor;
    min-height: 100vh;
    > .wrapper {
        padding: 50px 59px 50px;
    }
    &.add > .wrapper {
        padding: 155px 70px 50px;
    }
    &-title {
        margin: 0 0 50px;
        font-size: 32px;
        font-weight: 500;
        text-align: center;
    }
    &-close {
        position: absolute;
        right: 35px;
        margin-top: -15px;
        height: 25px;
        width: 25px !important;
        box-sizing: border-box;
        padding-left: 1px;
        overflow: hidden;
        letter-spacing: 2px;
        color: #000;
        &::before {
            font-size: 23px;
            line-height: 26px;
        }
        &:hover {
            color: #383838;
            text-decoration: none;
        }
    }
    &-back {
        position: absolute;
        left: 35px;
        margin-top: -15px;
        height: 25px;
        width: 25px !important;
        box-sizing: border-box;
        padding-left: 1px;
        overflow: hidden;
        letter-spacing: 2px;
        color: #0f9d58;
        &::before {
            font-size: 23px;
            line-height: 25px;
        }
        &:hover {
            color: #12bb69;
            text-decoration: none;
        }
    }
    .sticky-wrapper {
        height: 0 !important;
    }
    .step {
        .wrapper {
            padding: 50px 70px;
        }
        &-title {
            text-align: center;
            font-size: 32px;
            font-weight: 500;
            color: #383838;
            width: 800px;
            margin: 0 auto 50px;
        }
        &.final {
            .step{
                &-title {
                    font-size: 45px;
                    font-weight: 700;
                }
                &-subtitle {
                    line-height: 25px;
                    font-weight: 400;
                    width: 800px;
                    margin: 0 auto 50px;
                }
            }
        }
        &-subtitle {
            color: #383838;
            margin: -20px 0 40px;
            font-weight: 400;
        }
        &.final {
            .wrapper {
                padding-top: 150px;
            }
            &-title {
                font-size: 45px;
                font-weight: 700;
                color: #383838;
                margin: 0 0 50px;
            }
            p {
                text-align: center;
                .button {
                    width: 270px;
                    box-sizing: border-box;
                    text-align: center;
                    margin: 0 25px;
                    padding: 0;
                }
            }
        }
        .fieldset {
            margin: 0;
            padding: 0;
            border: 0;
        }
        .field {
            border-top: 2px solid #dedede;
            padding-top: 50px;
            margin-top: 50px;
            &:first-child {
                margin: 0;
                padding: 0;
                border: 0;
            }
            &-label {
                font-size: 24px;
                font-weight: 500;

            }
            &-body {
                margin-top: 15px;
                .inline {
                    display: inline-block;
                    margin-right: 20px;
                    vertical-align: top;
                    a {
                        line-height: 50px;
                        font-size: 17px;
                        font-weight: 400;
                    }
                }
                .deadline {
                    width: 400px;
                    select {
                        width: 100%;
                    }
                }
                .country {
                    width: 270px;
                    select {
                        width: 100%;
                    }
                }
                .city {
                    width: 330px;
                }
                .zip {
                    width: 200px;
                }
                .phone {
                    width: 270px;
                    position: relative;
                }
                .code {
                    width: 115px;
                }

                .brand-url {
                    display: block;
                    float: left;
                    width: 164px;
                    opacity: 0.5;
                    font-size: 18px;
                    line-height: 50px;
                    color: #7f8c8d;

                    & + .field-input-text {
                        float: left;
                        width: 385px;
                        margin-left: 65px;
                    }
                }
            }
            & p {
                margin: 15px 0 20px;
                font-weight: 400;
                color: #4c4c4c;
            }
            & p + .redactor-box {
                margin-top: 40px;
            }
            &-input {
                &-text {
                    border: 1px solid #dcdcdc;
                    border-radius: 7px;
                    width: 700px;
                    textarea {
                        resize: none;
                        height: 100px;
                        padding: 15px;
                    }
                    &.ib {
                        width: 400px;
                        display: inline-block;
                        margin-right: 16px;
                    }
                }

                &-tag {
                    display: block;
                    height: 48px;
                    background: none;
                    border: 0;
                    padding: 0 15px;
                    font-weight: 400;
                    font-size: 18px;
                    line-height: 1.3;
                    box-sizing: border-box;
                    width: 100%;
                    outline-color: #1abc9c;
                    &:focus::placeholder {
                        opacity: 0;
                    }
                }
            }
            &.required {
                .field-label::after {
                    content: " *";
                }
            }
            &.sms-code {
                .button {
                    min-width: 190px;
                    box-sizing: border-box;
                    padding: 0 15px;
                    text-align: center;
                    height: 50px;
                    line-height: 50px;
                }
                .inline {
                    b,
                    span {
                        display: block;
                        font-size: 18px;
                        font-weight: 400;
                        line-height: 25px;
                        a {
                            line-height: 25px;
                        }
                    }
                    small {
                        display: block;
                        line-height: 1.2;
                        a {
                            font-size: 100%;
                        }
                    }
                }
            }
            .tile-radio {
                &-input {
                    display: none;
                    &:checked + .tile-radio-label {
                        background: #f3f3f3;
                        &::before {
                            border: solid 2px #0f9d58;
                            background: #0f9d58;
                        }
                    }
                }
                &-label {
                    height: 110px;
                    display: flex;
                    align-items: center;
                    box-sizing: border-box;
                    padding: 15px 15px 15px 80px;
                    border-radius: 8px;
                    font-weight: 400;
                    &::before {
                        content: "";
                        position: absolute;
                        top: 50%;
                        transform: translate(0, -11px);
                        left: 30px;
                        width: 22px;
                        height: 22px;
                        box-sizing: border-box;
                        border-radius: 50%;
                        border: solid 2px #5a5a5a;
                        background: #fff;
                        cursor: pointer;
                    }
                    b {
                        display: block;
                        font-size: 18px;
                        font-weight: 400;
                        margin: 0 0 5px;
                    }
                    p {
                        font-size: 15px;
                        line-height: 1.47;
                        color: #4c4c4c;
                        margin-bottom: 5px;
                        margin-top: 0;
                    }
                }

                &-label-rubber {
                    height: 150px;
                    padding-top: 39px;
                    padding-bottom: 35px;

                }
            }
            .select2-container--default .select2-selection--multiple,
            .select2-container--default .select2-selection--single {
                border: 1px solid #dcdcdc;
                font-size: 18px;
                box-sizing: border-box;
                height: 50px;
                width: 100%;
                .select2-selection__rendered {
                    line-height: 48px;
                    font-weight: 400;
                    .select2-selection__placeholder {
                        color: #7f8c8d;
                    }
                }
                .select2-selection__arrow {
                    top: 29px;
                    right: 12px;
                }
            }
            .tag-wrapper {
                margin-top: 20px;
                .tag-item {
                    padding: 15px 14px;
                    line-height: 1.22;
                    color: #4c4c4c;
                    font-size: 18px;
                    border-radius: 8px;
                    background-color: #f3f3f3;
                    font-weight: 400;
                    display: inline-block;
                    margin-left: 16px;
                    &:first-child {
                        margin-left: 0;
                    }
                    .remove-tag {
                        text-decoration: none;
                        color: #bf1b39;
                        font-size: 13px;
                        margin-left: 10px;
                    }
                }
            }
            .add-category-wrapper {
                border-radius: 8px;
                background-color: #f3f3f3;
                padding: 20px;
                margin-bottom: 20px;
                .category-select {
                    background: #fff;
                    width: 30%;
                    display: inline-block;
                    margin-left: 21px;
                    position: relative;
                    border-radius: 7px;
                    max-height: 50px;
                    select {width: 100%;}
                    &:first-child {
                        margin-left: 0;
                    }
                    .select2-container {

                    }
                    .select2-container--tags {
                        height: 50px;
                        border: 1px solid #dcdcdc;
                        border-radius: 7px;
                    }
                    .select2-selection--multiple input {
                        cursor: pointer;
                        &:focus::placeholder {
                            opacity: 1;
                        }
                    }
                }
                .remove-row {
                    text-decoration: none;
                    color: #bf1b39;
                    font-size: 13px;
                    margin-left: 26px;
                    margin-top: 20px;
                }
            }
            .options-product {
                .row {
                    border-radius: 8px;
                    background-color: #f3f3f3;
                    padding: 20px;
                    margin-bottom: 20px;
                    &.bg-transparent {
                        background-color: transparent;
                        padding: 0 20px;
                        margin-bottom: 10px;
                        .field-value {
                            font-size: 16px;
                            font-weight: 500;
                            .name {
                                position: relative;
                                display: inline-block;
                                .icn-help {
                                    display: inline-block;
                                    position: relative;
                                    & + .tooltip {
                                        display: none;
                                        position: absolute;
                                        background: #fff;
                                        box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.5);
                                        border-radius: 7px;
                                        padding: 10px 15px;
                                        color: #7f8c8d;
                                        font-weight: 400;
                                        text-align: left;
                                        min-width: 300px;
                                        font-size: 16px;
                                        z-index: 98;
                                        margin-left: 15px;
                                        top: -15px;
                                        left: 100%;
                                    }
                                    & + .tooltip:after,
                                    & + .tooltip:before {
                                        right: 100%;
                                        top: 25px;
                                        border: solid transparent;
                                        content: ' ';
                                        height: 0;
                                        width: 0;
                                        position: absolute;
                                        pointer-events: none;
                                    }

                                    & + .tooltip:after {
                                        border-color: rgba(136, 183, 213, 0);
                                        border-right-color: #fff;
                                        border-width: 5px;
                                        margin-top: -5px;
                                    }
                                    & + .tooltip:before {
                                        border-color: rgba(194, 225, 245, 0);
                                        border-right-color: rgba(0, 0, 0, 0.5);
                                        border-width: 6px;
                                        margin-top: -6px;
                                    }
                                    &:hover + .tooltip {display: block;}
                                }
                            }

                        }
                    }
                    table {
                        width: 100%;
                        border-collapse: collapse;
                        .field-value {
                            line-height: 1.22;
                            color: #4c4c4c;
                            font-size: 18px;
                            font-weight: 400;
                        }
                        .field-input-text {
                            background: #fff;
                            width: auto;
                            position: relative;
                            .help-text {
                                position: absolute;
                                top: 15px;
                                right: 10px;
                                line-height: 1.38;
                                color: #4c4c4c;
                                font-size: 16px;
                                font-weight: 400;
                            }
                        }
                        tr {
                            td {
                                padding: 0 25px 0 0; text-align: center;
                                .remove-row {
                                    text-decoration: none;
                                    color: #bf1b39;
                                    font-size: 13px;
                                    margin-left: 22px;
                                    margin-top: 5px;
                                }
                                &:nth-child(1) {
                                    width: 360px;
                                    text-align: left;
                                }
                                &:nth-child(2) {
                                    width: 140px;
                                }
                                &:nth-child(3) {
                                    width: 170px;
                                }
                                &:nth-child(4) {
                                    width: 120px;
                                }
                                &:nth-child(5) {
                                    width: 120px;
                                }
                                &:nth-child(6) {
                                    width: 160px;
                                    padding-right: 0;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    &-content.wrapper {
        padding-bottom: 150px;
    }

    &-footer {
        box-sizing: border-box;
        position: fixed;
        height: 75px;
        bottom: 0;
        left: 0;
        right: 0;
        padding-top: 15px;
        background: #f5f5f5;
        text-align: right;
        z-index: 100;
        box-shadow:  rgba(0, 0, 0, 0.3);

        * {
            box-sizing: border-box;
        }

        .wrapper {
            padding-left: 70px;
            padding-right: 70px;
        }

        .button {
            margin: 0;
            margin-left: 11px;
            height: 45px;
            line-height: 45px;

            &-grey {
                background-color: #7f8c8d;

                &:hover {
                    background-color: #6a7475;
                }
            }
        }
    }

    .collections-row {
        position: relative;
        display: block;
        width: 100%;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: start;
        -ms-flex-align: start;
        align-items: flex-start;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        margin-bottom: 50px;

        hr {
            display: block;
            width: 100%;
            height: 2px;
            background: #dedede;
            margin: 0 18px;
            margin-top: 50px;
            border: none;
        }
    }
}
