.overlay {
	position: fixed;
	display: none;
	z-index: 1200;
	left: 0;
	top: 0;
	right: 0;
	bottom: -500px;
	background: rgba(0, 0, 0, .3);
}
.popup {
	display: none;
	position: relative;
	z-index: 1300;
	margin: 50px auto;
	background: #fff;
	border-radius: 10px;
	box-shadow: 0 2px 5px 2px rgba(0, 0, 0, 0.3);
	padding: 50px;
	box-sizing: border-box;
	&-shown {
		overflow: hidden;
		.popup-wrap {
			display: block;
		}
	}
	&-wrap {
		display: none;
		overflow-x: hidden;
		overflow-y: scroll;
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 2000;
	}
	&-title {
		color: #383838;
		font-size: 32px;
		font-weight: 500;
		text-align: center;
		margin: 0 0 50px;
		@media screen and (max-height: 700px) {
			margin: 0 0 30px;
		}
	}
	.close {
		position: absolute;
		right: -55px;
		top: 5px;
		width: 25px;
		height: 25px;
		font-size: 25px;
		color: #fff;
		cursor: pointer;
	}
	.back {
		position: absolute;
		left: 20px;
		top: 20px;
		width: 25px;
		height: 25px;
		font-size: 25px;
		color: #0f9d58;
		cursor: pointer;
	}
	&-footer {
		background: #f3f3f3;
		margin: 30px -50px -50px;
		border-radius: 0 0 10px 10px;
		padding: 20px 50px;
		font-size: 18px;
		text-align: center;
		font-weight: 500;
	}
	&-info {
		text-align: center;
		line-height: 1.6;
		margin: 20px 0 0;
		font-weight: 400;
		color: #383838;
		&.error {
			color: #cd0a0a;
		}
	}
	&.wide {
		width: 85%;
		min-width: 850px;
	}
	&.compact {
		width: 520px;
    @media only screen and (min-height: 600px) {
      top: 50%;
      margin: 0 auto;
      transform: translateY(-50%);      
    }
		.button {
			width: 100%;
			display: block;
			margin: 0 0 20px;
			position: relative;
			&:first-child {
				margin: 0;
			}
			&.icon {
				&::before {
					position: absolute;
					left: 20px;
					top: 50%;
					transform: translate(0, -50%);
					font-size: 29px;
				}
				&.icon-btn-vk {
					background: #55779b;
					&:hover {
						background: darken(#55779b, 5%);
					}
				}
				&.icon-btn-g {
					background: #dd4b39;
					&:hover {
						background: darken(#dd4b39, 5%);
					}
				}
			}
		}
	}
	&.medium {
		width: 600px;
		top: 50%;
		margin: 0 auto;
		padding: 70px;
		padding: 55px;
		text-align: center;
		transform: translateY(-50%);
		p {
			margin-bottom: 30px;
			line-height: 1.56;
		}
	}

	&-buttons {
		display: flex;
		justify-content: space-between;
		margin-top: 30px;
		.button {
			width: 220px;
		}
	}

	&-scroll {
		&-content {
			@include posa(0, 148px, 0, 50px);
			overflow: auto;
			padding: 0 50px;
			.content-toolbar + & {
				top: 291px;
			}
		}
	}

	&-add-set,
	&-add-collection,
	&-support {
		height: auto;
		margin: 30px auto;
		text-align: center;

		@media screen and (max-height: 700px) {
			padding-top: 24px;
		}

    @media only screen and (min-height: 501px) {
      top: 50%;
      margin: 0 auto;
      transform: translateY(-50%);      
    }

		.button {
			display: inline-block;
			width: auto;
			height: 55px;
			line-height: 55px;
		}
	}
	&-add-set {
		max-width: 580px;

		.popup-title {
			margin-bottom: 16px;
		}
		p {
			margin: 0 0 29px;
			color: #4c4c4c;
			font-size: 16px;
			line-height: 1.56;
			text-align: center;
		}
		.icon {
			position: relative;
			top: 3px;
			padding: 5px;
			color: #4c4c4c;
			font-size: 23px;
			vertical-align: middle;
		}
		.field-btn {
			margin-top: 30px;
		}
		.button {
			width: 220px;
		}
	}
	&-add-collection {
		max-width: 1268px;

		.button {
			min-width: 260px;
			margin: 0 10px;
		}
	}
	&-support {
		max-width: 900px;
    min-width: 910px; 

		&-info {
			float: left;
			width: 35%;
			padding: 32px 20px;
			background-color: #f3f3f3;
			@media screen and (max-height: 700px) {
				padding: 23px 20px;
			}

			ul {
				margin: 22px 0;
				list-style: none;
			}
			li:before {
				position: relative;
				content: "-";
				padding-right: 4px;
				font-size: 16px;
				line-height: 1.38;
			}
			p, li {
				color: #4c4c4c;
				font-size: 16px;
				line-height: 1.38;
				text-align: left;
				@media screen and (max-height: 700px) {
					font-size: 15px;
				}
			}
		}
		&-title {
			padding: 18px 25px 5px;
			color: #383838;
			font-size: 20px;
			font-weight: 500;
			text-align: center;
			@media screen and (max-height: 700px) {
				font-size: 16px;
			}
		}
		&-form {
			float: right;
			width: 55%;
			@media screen and (max-height: 700px) {
				textarea {
					height: 210px;
				}
			}

			.button {
				min-width: 210px;
			}
		}
		&-user {
			height: 75px;
			margin-bottom: 20px;
		}
		&-avatar {
			position: absolute;
			display: block;
			width: 75px;
			height: 75px;
		}
		&-author {
			float: left;
			width: 80%;
			padding-left: 105px;
			color: #383838;
			font-size: 25px;
			font-weight: 500;
			line-height: 73px;
			text-align: left;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
		}
		&-btns {
			margin-top: 30px;
		}
	}

	&-remove-brand {
		text-align: center;

		p {
			font-size: 16px;
			font-weight: 400;
			line-height: 1.56;
			padding-bottom: 1.56 * 16px;
			color: #383838;
			margin: 0;
		}

		&-buttons {
			margin-top: 25px;
			margin-bottom: 17px;

			.button {
				margin: 0 10px;
				height: 55px;
				line-height: 55px;
			}
		}
	}
}
