.redactor {
	&-box {
		margin-top: -20px;
	}
	&-editor {
		color: #383838;
		&::after,
		p,
		blockquote,
		h1,
		h2,
		h3,
		h4,
		h5,
		h6 {
			font-family: $fontRoboto;
			font-size: 16px;
		}
		&::after {
			position: absolute;
			left: 50%;
			top: 50%;
			width: 400px;
			text-align: center;
			transform: translate(-200px, -22px);
		}
	}
	&-toolbar {
		font-size: 20px !important;
		li a {
			padding: 20px;
		}
	}
}
