.content {
  &-part {
    position: relative;
    background: #fff;
    color: #383838;
    .wrapper {
      padding: 50px 70px;
      position: relative;
    }
    &-header {
      text-align: center;
      margin: 0;
      padding: 75px 0 0;
      font-size: 32px;
      font-weight: 500;
      small {
        font-size: 24px;
        font-weight: 400;
      }
    }
    &-odd {
      background: #f3f3f3;
    }
    &-empty,
    &-empty-tab {
      .wrapper {
        text-align: center;
      }
      h2 {
        font-size: 32px;
        font-weight: 500;
        margin: 0 0 35px;
      }
      p {
        font-size: 16px;
        line-height: 25px;
        font-weight: 400;
      }
      .content-toolbar {
        position: relative;
        top: -100px;
      }
    }
    &-empty {
      &-body {
        max-width: 890px;
        margin: 0 auto;
        padding: 95px 70px 119px;
        text-align: center;
      }
      &-section {
        margin-bottom: 55px;
        text-align: center;
        p {
          line-height: 1.56;
        }
        .button {
          margin-top: 20px;
        }
      }
      .wrapper {
        padding: 150px 70px;
      }
    }
    &-empty-tab {
      .wrapper {
        padding: 95px 70px 119px;
      }
    }
  }
  &-toolbar {
    background: #8a9397;
    height: 93px;
    margin-bottom: 55px;
    position: relative;
    display: flex;
    justify-content: space-between;
    &-view {
      position: relative;
      display: block;
      width: 43px;
      min-width: 43px;
      height: 43px;
      margin: 25px 25px 0 25px;
      background-color: #fff;
      border-radius: 7px;
      &:before,
      &:after {
        position: absolute;
        display: block;
        content: '';
        background-color: #0f9d58;
      }
      &:before {
        top: 19px;
        left: 9px;
        width: 26px;
        height: 5px;
        @media only screen and (max-width: 1150px) {
          top: 15px;
          left: 8px;
          width: 18px;
          height: 4px;
        }
      }
      &:after {
        top: 9px;
        left: 19px;
        width: 5px;
        height: 26px;
        @media only screen and (max-width: 1150px) {
          top: 8px;
          left: 15px;
          width: 4px;
          height: 18px;
        }
      }
      @media only screen and (max-width: 1150px) {
        margin-top: 15px;
        width: 34px;
        min-width: 34px;
        height: 34px;
      }
      &.is-opened:after {
        display: none;
      }
    }
    &-section {
      width: 25%;
      padding: 25px;
      @media only screen and (max-width: 1150px) {
        padding: 15px;
      }
      &.tabs {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 50%;
        a {
          opacity: .5;
          cursor: pointer;
          color: #fff;
          transition: opacity 0.3s ease;
          font-weight: 500;
          @media (min-width: 1151px) {
            font-size: 18px;
          }
          @media (min-width: 1441px) {
            font-size: 20px;
          }
          &:hover,
          &.active {
            opacity: 1;
          }
        }
      }
    }
    &-part {
      flex: 0 1 25%;
      padding: 25px 25px 25px 0;
      box-sizing: border-box;
      position: relative;
      text-align: left;
      &:first-child {
        margin-left: 25px;
      }
      &.content-toolbar-tabs {
        flex: 0 1 50%;
        align-self: center;
      }
      &.content-toolbar-middle {
        flex: 0 1 75%;
        text-align: left;
      }
      &.content-toolbar-full {
        flex: 0 1 100%;
        a {
          margin-left: 50px;
        }
      }
      &.left {
        margin-right: 47%;
      }
    }
    &-multi {
      padding-left: 15px;
    }
    &-search {
      border: 1px solid rgba(255, 255, 255, .25);
      border-radius: 7px;
      position: relative;
      &-text {
        height: 43px;
        background: none;
        border: 0;
        padding: 0 40px 0 15px;
        font-size: 18px;
        outline: 0;
        color: #fff;
        box-sizing: border-box;
        width: 100%;
        &::placeholder {
          color: #fff;
          opacity: 1;
        }
      }
      &-button {
        position: absolute;
        box-sizing: border-box;
        right: 9px;
        top: 9px;
        padding-top: 1px;
        padding-left: 1px;
        border: 0;
        overflow: hidden;
        width: 26px;
        height: 26px;
        line-height: 24px;
        font-size: 24px;
        letter-spacing: 2px;
        background: none;
        color: #fff;
        cursor: pointer;
      }
    }
    &-tabs {
      margin: 0;
      padding: 15px;
      font-weight: 500;
      text-align: center;
      list-style: none;
      a {
        display: inline-block;
        margin-left: 28px;
        opacity: .5;
        cursor: pointer;
        color: #fff;
        transition: opacity 0.3s ease;
        &:hover {
          text-decoration: underline;
        }
        &:hover,
        &.active {
          opacity: 1;
        }
        &:first-of-type {
          margin-left: 0;
        }
      }
      &.content-toolbar-middle {
        a:first-of-type {
          margin-left: 13px;
        }
      }
      @media #{$desktop} {
        padding: 15px;
        font-size: 16px;
      }
      @media #{$desktop-up} {
        padding: 25px;
        font-size: 18px;
      }
      @media #{$desktop-large} {
        padding: 25px;
        font-size: 20px;
      }
    }
    &-sort {
      .select2-container--default {
        width: 100% !important;
        .select2-selection--single {
          border: 1px solid rgba(255, 255, 255, .25);
          color: #fff;
          font-size: 18px;
          box-sizing: border-box;
          height: 45px;
          width: 100%;
          .select2-selection__rendered {
            line-height: 43px;
          }
          .select2-selection__arrow {
            top: 27px;
            right: 12px;
            b {
              border-top-color: #fff;
            }
          }
        }
        &.select2-container--open {
          .select2-selection--single {
            background: #fff;
            .select2-selection__rendered {
              color: #383838;
            }
            .select2-selection__arrow {
              b {
                border-top-color: #000;
              }
            }
          }
        }
      }
    }
    &-range {
      padding: 25px 45px 25px 20px;
    }
    &.button-toolbar {
      align-items: center;
      .create-button {
        float: right;
        width: 100%;
        max-width: 310px;
        white-space: nowrap;
      }
    }
  }
  &-show {
    overflow: hidden;
    text-align: center;
    margin-top: 55px;
    .pager {
      display: inline-block;
      margin: 10px;
    }
    .pull-right {
      margin-left: -100%;
    }
  }
}

.content-tab {
  display: none;
  &.active {
    display: block;
  }
  &-empty {
    padding: 50px 0;
    text-align: center;
    strong {
      font-weight: 500;
    }
    h2 {
      font-size: 32px;
      font-weight: 500;
      margin: 0 0 35px;
    }
  }
}

.tile-list {
  margin: -18px;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  &-item {
    position: relative;
    width: 25%;
    box-sizing: border-box;
    border: 18px solid transparent;
    .brand-card {
      left: 30px;
    }
    &+ul {
      margin-top: 2px;
    }
    &.wide {
      width: 50%;
    }
    &.mid {
      width: 75%;
    }
    &.full {
      width: 100%;
      border: 0 none;
    }
    &.full-border {
      width: 100%;
    }
    &.goods-item-recommends {
      width: 50%;
      border: 0 none;
      &:nth-child(odd) {
        border: 18px solid transparent;
        border-right: 9px solid transparent;
      }
      &:nth-child(even) {
        border: 18px solid transparent;
        border-left: 9px solid transparent;
      }
    }
    &-state {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 0;
      text-align: center;
      color: #fff;
      font-size: 22px;
      font-weight: 500;
    }
    &-info {
      height: 2.6em;
      line-height: 1.3;
      margin-bottom: 1.3em;
    }
  }
  .banner-image {
    display: block;
    background-size: cover;
    background-position: 50% 50%;
    padding-top: 35.2%;
    &.high {
      padding-top: 100%;
    }
  }
}

.add-product-list {
  .tile-list-item {
    width: 20%;
  }
}

.text-part {
  .wrapper {
    box-sizing: border-box;
    max-width: 1000px;
  }
  img,
  picture {
    max-width: 100%;
  }
  h1,
  h2,
  h3 {
    margin: 35px 0 25px;
    &:first-child {
      margin-top: 0;
    }
  }
  p {
    margin: 25px 0 0;
    &:first-child {
      margin-top: 0;
    }
  }
}

.preview-mode {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 180px;
  background: #fff;
  color: #383838;
  h2 {
    font-size: 32px;
    font-weight: 500;
  }
}

