.social-share {
	margin-top: 35px;
	b {
		color: #383838;
		font-size: 22px;
		font-weight: 500;
		display: block;
		margin: 0 0 15px;
	}
	.icon {
		width: 74px;
		height: 74px;
		box-sizing: border-box;
		padding-left: 2px;
		padding-top: 4px;
		letter-spacing: 2px;
		margin: 0 20px;
		display: inline-block;
		overflow: hidden;
		text-decoration: none;
		&:hover {
			text-decoration: none;
		}
		&::before {
			font-size: 70px;
		}
		&-facebook {
			color: #3b5998;
			&:hover {
				color: darken(#3b5998, 5%);
			}
		}
		&-btn-vk {
			color: #55779b;
			&:hover {
				color: darken(#55779b, 5%);
			}
		}
		&-btn-g {
			color: #dd4b39;
			&:hover {
				color: darken(#dd4b39, 5%);
			}
		}
		&-twitter {
			color: #55acee;
			&:hover {
				color: darken(#55acee, 5%);
			}
		}
		&-tumblr {
			color: #283e58;
			&:hover {
				color: darken(#283e58, 5%);
			}
		}
	}
}
