.search {
  &-header {
    box-sizing: border-box;
    position: relative;
    display: flex;
    align-items: flex-end;
    height: 400px;
    color: #fff;
    background-size: cover;
    background-position: 50% 50%;
    &-title {
      margin: 0;
      font-size: 30px;
      line-height: 1.2;
      font-weight: 400;
    }
    .wrapper {
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      width: 100%;
      padding: 50px 70px;
    }
  }
  &-menu {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    background-color: $bg-color;
    list-style: none;
    padding-bottom: 20px;
    &-item {
      position: relative;
      width: 100px;
      height: 100px;
      margin: 20px 10px 0;
      background-size: cover;
      background-position: 50% 50%;
    }
    &-link {
      display: flex;
      justify-content: center;
      align-items: flex-end;
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      padding: 0 6px 12px;
      color: #fff;
      font-size: 15px;
      font-weight: 500;
      text-align: center;
      background-image: linear-gradient(to bottom, rgba(67, 67, 67, 0), rgba(0, 0, 0, 0.73));
      &:after {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background: #000;
        opacity: 0;
        transition: .4s ease opacity;
      }
      &:hover {
        color: #fff;
        &:after {
          opacity: .3;
        }
      }
    }
  }
  &-cat {
    display: flex;
    justify-content: center;
    list-style: none;
    margin-top: 60px;
    &-item {
      width: 16.666666667%;
    }
    &-link {
      position: relative;
      display: block;
      color: #000;
      &:before {
        position: absolute;
        display: block;
        content: '';
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
      }
      &:hover {
        color: $linkColorAlt;
        text-decoration: none;
      }
    }
    &-icon {
      font-size: 50px;
    }
    &-name {
      white-space: nowrap;
    }
  }

  .content-toolbar {
    justify-content: flex-start;
  }
  .page-menu {
    padding: 38px 0;
  }


  .capabilities {
    .cart-empty,
    .wrapper {
      height: 410px;
      position: relative;
      padding: 0;
      background: url(../img/laptop1.svg) 50% 42% no-repeat;
      background-size: 37% auto;
    }
    &-block-1,
    &-block-2 {
      position: absolute;
      p {
        max-width: 320px;
        color: #7f8c8d;
      }
    }
    &-block-1 {
      right: 70%;
      top: 135px;
      padding-left: 20px;
      text-align: right;
    }
    &-block-2 {
      left: 70%;
      top: 110px;
      padding-right: 20px;
      text-align: left;
    }
    &-title {
      color: #383838;
      font-weight: 500;
      margin: 0 0 10px;
      font-size: 21px;
    }
  }
}