.add-tool {
	position: relative;
	display: block;
	height: 0;
	padding-top: 100%;
	width: 100%;
	background: #617680;
	position: relative;
	overflow: hidden;
	color: #fff;
	z-index: 2; 
	&:hover {
		color: #fff;
		background: darken(#617680, 5%);
	}
	span {
		position: absolute;
		top: 50%;
		transform: translate(0, -41px);
		width: 100%;
		text-align: center;
		font-size: 20px;
		font-weight: 400;
		line-height: 24px;
		&::before {
			content: '';
			width: 8px;
			height: 48px;
			background: #fff;
			display: block;
			margin: 0 auto 10px;
		}
		&::after {
			content: '';
			width: 48px;
			height: 8px;
			background: #fff;
			position: absolute;
			top: 20px;
			left: 50%;
			transform: translate(-24px, 0);
		}
	}
	&-img {
		position: absolute;
    top: 0;
    left: 0;
		width: 100%;
		padding-top: 100%;
		background-repeat: no-repeat;
		background-size: contain;
		overflow: hidden;
	}
	&-input {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		cursor: pointer;
		opacity: 0;
	}
	.goods-item > & {
		padding-top: 113.79%;
	}
	.add-product-item > & {
		padding-top: 100%;
		span {
			left: 50%;
			width: 80%;
			font-size: 16px;
			font-weight: 400;
			transform: translate(-50%, -50%);
			user-select: none;
			@media #{$desktop} {
				color: transparent;
			}
			&::before {
				content: "\37";
				width: 87px;
				height: 48px;
				background: none;
				font-size: 78px;
				line-height: 48px;
				font-family: "icons";
				@media #{$desktop} {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					color: #fff;
					font-size: 64px;
				}
			}
			&::after {
				display: none;
			}
		}
	}
	.product-item > & {
		padding-top: 113.79%;
		span {
			top: 40%;
			font-size: 16px;
			font-weight: 400;
			transform: translate(0, -51px);
			&::before {
				content: "\37";
				width: 87px;
				height: 48px;
				background: none;
				font-size: 78px;
				line-height: 48px;
				font-family: "icons";
			}
			&::after {
				display: none;
			}
		}
	}
	.collection-item > & {
		padding-top: 67.2%;
	}
	.add-collection > &,
	.banner-item > & {
		span {
			font-size: 16px;
			font-weight: 400;
			transform: translate(0, -51px);
			&::before {
				content: "\37";
				width: 87px;
				height: 48px;
				background: none;
				font-size: 78px;
				line-height: 48px;
				font-family: "icons";
			}
			&::after {
				display: none;
			}
		}
	}
	.add-collection > & {
		height: 398px;
		padding-top: 0;
		@media only screen and (max-width: 1150px) {
			height: 290px;
		}
	}
	.banner-item > & {
		padding-top: 35.2%;
	}
}
