.step-meter {
  margin: 0;
  padding: 35px 0 30px;
  list-style: none;
  display: flex;
  justify-content: center;
  background: #f3f3f3;
  .orders & {
    padding: 0;
  }
  &-item {
    white-space: nowrap;
    color: #383838;
    font-size: 20px;
    font-weight: 500;
    width: 226px;
    text-align: center;
    overflow: hidden;
    position: relative;
    @media only screen and (max-width: 1150px) {
      font-size: 19px;
    }
    &::before {
      content: '';
      position: absolute;
      height: 3px;
      width: 60px;
      border: 1px solid #fff;
      border-width: 0 200px;
      top: 19px;
      left: 50%;
      transform: translate(-50%, 0);
    }
    &:first-child::before {
      border-left-color: transparent !important;
    }
    &:last-child::before {
      border-right-color: transparent !important;
    }
    &.active {
      color: #ff5722;
      font-weight: 500;
      &::before {
        border-left-color: #0f9d58;
      }
      .step-meter-link {
        cursor: default;
      }
      .step-meter-dot {
        background: #ff5722;
        border-color: #ff5722;
        color: #fff;
        font-weight: 500;
      }
      .step-meter-text {
        color: #ff5722;
        text-decoration: none !important;
      }
    }
    &.done {
      &::before {
        border-left-color: #0f9d58;
        border-right-color: #0f9d58;
      }
      .step-meter-dot {
        color: #0f9d58;
        border-color: #0f9d58;
        text-indent: 40px;
        &::before {
          content: "\49";
          position: absolute;
          left: 6px;
          top: 7px;
          width: 22px;
          height: 22px;
          color: #0f9d58;
          font: normal 24px/24px 'icons';
          text-indent: 0;
        }
      }
    }
    .orders & {
      width: 25%;
      white-space: normal;
      font-size: 16px;
      font-weight: 400;
      &:before {
        border-width: 0 125px;
      }
      &.active {
        font-weight: 500;
        .step-meter-dot {
          font-weight: 700;
        }
      }
      .step-meter-text {
        max-width: 170px;
        margin: 0 auto;
      }
    }
  }
  &-link,
  &-link:hover {
    color: #383838;
    display: block;
    text-decoration: none;
    position: relative;
  }
  &-link:hover {
    .step-meter-text {
      text-decoration: underline;
    }
  }
  &-dot {
    background: #fff;
    width: 40px;
    height: 40px;
    font-size: 27px;
    line-height: 35px;
    margin: 0 auto 10px;
    border: 2px solid #fff;
    border-radius: 50%;
    box-sizing: border-box;
    overflow: hidden;
    position: relative;
    .orders & {
      font-size: 22px;
      font-weight: 400;
    }
  }
}