.toolbar {
	position: absolute;
	z-index: 1100;
	width: 100%;
	left: 0;
	transform: translateZ(0);
	min-width: 1000px;
	box-sizing: border-box;
	&::before {
		content: '';
		background: #fff;
		opacity: 0;
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		transition: opacity ease .2s;
		box-shadow: 0 0 20px #000;
	}
  .pseudo-popup & {
    position: relative;
  }
	.wrapper {
		position: relative;
		max-width: 100%;
		height: 75px;
	}
	.logo-link {
		position: absolute;
		top: 20px;
	}
	.logo {
		pointer-events: none;
	}
	&.fixed {
		position: fixed;
		top: 0;
	}
	&.bright {
		&::before {
			opacity: 1;
		}
	}
	&.shadowless {
		&::before {
			display: none;
		}
	}
	&.dark {
		background: rgba(0,0,0, .25);
	}
}

.goods-tools {
	position: absolute;
	left: 180px;
	top: 18px;
	display: flex;
	width: 49%;
	max-width: 630px;
}

.tools {
	&-item {
		float: left;
		margin-right: 10px;
		.user-tools & {
			margin: 0 0 0 15px;
		}
	}
	&-search {
		flex: 1 1 100%;
		position: relative;
		width: 30%;
		max-width: 360px;
		height: 36px;
		border-radius: 7px;
		&-text {
			width: 100%;
			color: #fff;
			font-size: 15px;
			outline: 0;
			box-sizing: border-box;
			&::placeholder {
				color: #fff;
				opacity: 1;
			}
		}
		&-button {
			position: absolute;
			right: 8px;
			top: 8px;
			background: none;
			color: #fff;
			border: 0;
			width: 22px;
			height: 20px;
			padding: 0 0 0 1px;
			font-size: 20px;
			overflow: hidden;
			vertical-align: middle;
			letter-spacing: 2px;
			z-index: 1;
			&.gray {
				color: #4c4c4c;
			}
		}
		.shop-account &,
		.bright & {
			border-color: #d3d3d3;
			&-text {
				color: #4c4c4c;
				&::placeholder {
					color: #4c4c4c;
				}
			}
			&-button {
				color: #4c4c4c;
			}
		}
	}
	&-dropbox {
		position: relative;
		font-size: 15px;
		min-width: 120px;
		text-align: left;
		&-button {
			border: 1px solid rgba(255, 255, 255, .25);
			color: #fff;
			border-radius: 7px;
			box-sizing: border-box;
			height: 38px;
			line-height: 36px;
			padding: 0 35px 0 15px;
			min-width: 120px;
			display: block;
			position: relative;
			cursor: pointer;
			transition: all .15s;
			&::before {
				content: '';
				background: rgba(0, 0, 0, 0);
				position: absolute;
				left: -1px;
				right: -1px;
				top: 30px;
				height: 30px;
			}
			&::after {
				content: '';
				position: absolute;
				right: 10px;
				top: 15px;
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 8px 8px 0;
				border-color: #fff transparent transparent transparent;
				transition: all .15s;
			}
		}
		&-list {
			position: absolute;
			top: 46px;
			margin: 0;
			padding: 0;
			list-style: none;
			left: 0;
			z-index: 15;
			font-weight: 400;
			min-width: 100%;
			border-radius: 7px;
			box-sizing: border-box;
			background: #fff;
			margin: 0;
			box-shadow: 0 0 15px rgba(0, 0, 0, .3);
			overflow: hidden;
			opacity: 0;
			visibility: hidden;
			transition: all .15s;
			.user-tools & {
				left: auto;
				right: 0;
			}
		}
		&-new-part {
			border-top: 1px solid #d0d0d0;
		}
		&-link {
			display: block;
			padding: 0 45px 0 15px;
			line-height: 38px;
			color: #000;
			white-space: nowrap;
			text-decoration: none;
			position: relative;
			&:hover {
				background: #ff5722;
				color: #fff;
				text-decoration: none;
			}
		}
		&-counter {
			position: absolute;
			right: 15px;
			top: 5px;
			padding: 0 5px;
			font-size: 15px;
			color: #fff;
			background: #4c4c4c;
			height: 28px;
			line-height: 28px;
			border-radius: 5px;
		}
		&.hover {
			.tools-dropbox {
				&-button {
					background: #fff;
					color: #4c4c4c;
					&::after {
						border-top-color: #4c4c4c;
					}
				}
				&-list {
					visibility: visible;
					opacity: 1;
				}
			}
		}
		.shop-account &,
		.bright & {
			&-button {
				border-color: #d3d3d3;
				background: #fff;
				color: #4c4c4c;
				&::after {
					border-top-color: #4c4c4c;
				}
			}
		}
		.shop-account & {
			&-button {
				background-color: transparent;
			}
		}
	}
	&-link {
		white-space: nowrap;
		color: #fff;
		&:hover {
			color: $linkColor;
		}
		.shop-account &,
		.bright & {
			color: #4c4c4c;
		}
		&.icon {
			text-decoration: none;
			&:hover {
				color: $linkColor;
			}
			&.badge-notification {
				width: auto;
				margin-right: -22px;
			}
		}
	}
}

.user-tools {
	position: absolute;
	right: 30px;
	top: 18px;
	line-height: 38px;
	.icon {
		position: relative;
		display: inline-block;
		font-size: 28px;
		padding-left: 1px;
		vertical-align: middle;
		width: 30px;
		letter-spacing: 2px;
		&::before {
			line-height: 38px;
		}
	}
	.login {
		margin-left: 35px;
		font-weight: 400;
	}
}
