.message {
		&-edit {
				display: none;
		position: relative;
				margin: 0;
		padding: 0 0 50px 120px;
				.redactor-box {
						margin: 0;
				}
				.redactor-toolbar li a {
						padding-top: 25px;
						padding-bottom: 25px;
				}
				.message-body {
						padding-right: 0;
						cursor: default;
						&:hover {
								background: transparent;
						}
				}
				.buttons {
						position: absolute;
						right: 25px;
						margin-top: 10px;
						z-index: 101;
						.button {
								margin-left: 4px;
								&.yellow {
									position: absolute;
									right: 152px;
								}
								&.gray {
									position: absolute;
									right: 0;
								}
						}
				}
		}
	&-group {
		position: relative;
		margin: 50px 0 0;
		padding: 0 0 50px;
		border-bottom: 2px solid #dedede;
		&:first-child {
			padding-top: 0;
			margin-top: 0;
		}
				.item-tools {
						top: auto;
						margin-top: 15px;
						.icon {
								color: #383838;
						}
						&.more-active .icon-more,
						.icon:hover {
								color: #0f9d58;
						}
				}
				.tools-dropbox-list {
						right: 0;
						left: auto;
						min-width: 200px;
				}
				&.active {
						.message-edit,
						.message-item {
								display: block;
						}
						.item-tools {
								display: none;
						}
				}
	}
		&-list {
				margin: 0;
				padding: 0;
		list-style: none;
		}
	&-item {
				display: none;
		position: relative;
				margin: 0;
		padding: 50px 0 0 120px;
				&:first-child {
						display: block;
						padding-top: 0;
				}
	}
	&-body {
		position: relative;
				padding: 10px 20px 15px;
				cursor: pointer;
				&:hover {
						background: #f3f3f3;
				}
	}
	&-image {
		position: absolute;
				left: 0;
		width: 100px;
		height: 100px;
	}
	&-author {
		margin-right: 25px;
		font-size: 25px;
		font-weight: 500;
				word-break: break-word;
		a {
			color: inherit;
		}
	}
	&-date {
		color: #7f8c8d;
		font-size: 14px;
		font-weight: 400;
		line-height: 1.4;
	}
	&-text {
		color: #7f8c8d;
		margin: 15px 0 0;
		p {
			margin: 15px 0 0;
			&:first-child {
				margin: 0;
			}
		}
	}
		&-new {
				padding-left: 150px;
				.message {
						&-image {
								border-left: 15px solid #e91e63;
								padding-left: 15px;
						}
						&-text {
								color: #383838;
						}
				}
		}
		&-blocked {
				&-sign {
						display: none;
						margin-left: 20px;
						padding: 3px 20px 6px;
						font-size: 16px;
						font-weight: normal;
						background: #383838;
						color: #fff;
						vertical-align: bottom;
				}
				.message {
						&-blocked-sign {
								display: inline-block;
						}
						&-author a {
								text-decoration: line-through;
						}
						&-text {
								color: #383838;
						}
				}
		}
}
.user-select {
		width: 390px;
		visibility: hidden;
}
.select2-container--users {
		float: right;
		height: 50px;
		.select2 {
				&-selection {
						float: right;
						width: 100%;
						max-width: 310px;
						padding: 0;
						outline: none;
						&__rendered {
								background: #0f9d58;
								border-radius: 7px;
								line-height: 49px;
								height: 50px;
								font-size: 17px;
								color: #fff;
								font-weight: 400;
								cursor: pointer;
								text-align: center;
								&:hover {
										background: darken(#0f9d58, 5%);
								}
						}
						&__arrow {
								display: none;
						}
						&__clear {
								display: none;
						}
				}
				&-search {
						position: relative;
						padding: 15px 20px;
						border-bottom: 1px solid #d7d7d7;
						&__field {
								position: relative;
								padding: 0;
								padding-right: 20px;
								outline: none;
								border: none;
						}
						&:after {
								position: absolute;
								top: 50%;
								margin-top: -.5em;
								content: '\48';
								right: 18px;
								font-family: 'icons';
								font-size: 20px;
								line-height: 1;
								-webkit-font-smoothing: antialiased;
								-moz-osx-font-smoothing: grayscale;
						}
				}
				&-dropdown {
						margin-top: 10px;
						border-radius: 7px;
						background: #fff;
						border: none;
						box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
				}
				&-results {
						&__options {
								max-height: 300px;
								overflow-y: auto;
						}
						&__option {
								border-top: 1px solid #d7d7d7;
								&:first-child {
										border-top: none;
								}
								img {
										margin: 9px 20px 9px 16px;
										width: 50px;
										height: 50px;
										vertical-align: middle;
								}
								&:hover {
										background: #ff5722;
										color: #fff;
								}
						}
						&__message {
								padding: 14px 20px 16px;
								&:hover {
										background: inherit;
										color: inherit;
								}
						}
				}
		}
}