.public {
  &-header {
    margin-bottom: 40px;
    text-align: center;
  }

  &-author {
    font-size: 18px;
    font-weight: 500;
    color: $linkColor;
  }

  &-date {
    margin-top: 6px;
    font-size: 18px;
    color: $txt-grey;
  }

  &-body {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.56;
    text-align: center;

    h3 {
      font-size: 20px;
      font-weight: 400;
    }

    img {
      max-width: 100%;
      vertical-align: middle;
    }
  }

  &-answer {
    textarea {
      height: 163px;
    }
  }

  .button {
    width: 280px;
    margin: 20px 0 50px;
  }
}