.pager {
	overflow: hidden;
	&-item {
		border: 1px solid #bbb;
		border-left-width: 0;
		float: left;
		box-sizing: border-box;
		height: 36px;
		line-height: 34px;
		min-width: 36px;
		padding: 0 7px;
		text-align: center;
		font-size: 14px;
		color: #383838;
		&:first-child {
			border-radius: 4px 0 0 4px;
			border-left-width: 1px;
		}
		&:last-child {
			border-radius: 0 4px 4px 0;
		}
	}
	&-active {
		background: #8a9397;
		color: #fff;
	}
	&-link {
		color: #383838;
		&:hover {
			color: #383838;
			text-decoration: none;
			background: #eee;
		}
	}
	&-prev,
	&-next {
		width: 36px;
		overflow: hidden;
		position: relative;
		text-indent: 36px;
		&::before {
			position: absolute;
			line-height: 32px;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			text-align: center;
			color: #bbb;
			text-indent: 0;
		}
	}
	&-prev::before {
		content: "\25c0 \FE0E";
		left: -2px;
	}
	&-next::before {
		content: "\25b6 \FE0E";
		right: -2px;
	}
}
