.shop-account {
	box-sizing: border-box;

	*,
	*::after,
	*::before {
		box-sizing: border-box;
		outline: none;
	}

	.tree-multiselect-dropbox {
		box-sizing: content-box;
		& *,
		& *::after,
		& *::before {
			box-sizing: content-box;
		}
	}

	hr {
		display: block;
		height: 2px;
		width: 100%;
		margin-top: 2.4em;
		margin-bottom: 3.1em;
		background: #dedede;
		border: none;
	}

	.header {
		height: 400px;
		background-size: cover;
		background-position: 50% 50%;
		background-repeat: no-repeat;
		background-color: #fff;
		display: flex;
		align-items: center;
		justify-content: center;

		p {
			font-size: 18px;
			line-height: 1.2;
			text-align: center;
			color: #4c4c4c;
			margin: 0;
			font-weight: 400;
		}

		.wrapper {
			position: relative;
			width: 100%;
			padding: 50px 0 0;
			margin: 0;
		}

		&-slick {
			height: 575px;
			position: relative;
			align-items: flex-end;

			.wrapper {
				padding: 50px;
			}

			a,
			a:hover {
				color: #fff;
			}

			h1,
			p {
				text-align: left;
				display: block;
			}
			p {
				font-size: 22px;
				font-weight: 400;
			}
		}

		&-slider {
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;

			&-image {
				position: relative;
				height: 575px;
				background-size: cover;

				&::before {
					content: '';
					position: absolute;
					left: 0;
					right: 0;
					bottom: 0;
					height: 100%;
					background: -webkit-linear-gradient(transparent, rgba(0, 0, 0, 0.175));
					background: linear-gradient(transparent, rgba(0, 0, 0, 0.175));
				}
			}
		}
		&-guest {
			width: 100%;
			margin: 0 auto;
			padding-top: 38px;
			text-align: center;
			&-icon {
				color: #000;
				font-size: 114px;
				text-align: center;
			}
			&-btn {
				width: 170px;
				margin-top: 20px;
			}
			p {
				font-size: 18px;
				text-align: center;
				color: #4c4c4c;
			}
		}
	}

	.header img {
		display: block;
		max-width: 127px;
		margin: 0 auto;
	}

	.header-title {
		display: block;
		padding-top: 10px;
		padding-bottom: 3px;
		margin: 0;
		text-align: center;
		font-size: 30px;
		font-weight: 500;
		color: #4c4c4c;
	}

	.drag-n-drop {
		height: 500px;
		background: #fff;
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;

		&-icon {
			position: relative;
			width: 107px;
			height: 107px;
			margin: 0 auto 30px;
			&::before {
				font-size: 107px;
			}
		}

		.wrapper {
			margin: 0;
		}

		input[type="file"] {
			display: block;
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			cursor: pointer;
			opacity: 0;
			font-size: 0;
		}
	}

	.store-image {
		position: relative;

		&-overlay {
			position: absolute;
			top: 0;
			bottom: 0;
			right: 0;
			left: 0;
			opacity: 0.85;
			background-color: #a1887f;
			z-index: 0;
		}

		.wrapper {
			position: relative;
			z-index: 1;
		}

		h2 {
			font-size: 32px;
			line-height: 38px;
			font-weight: 500;
			color: #fff;
			margin: 0;
			padding-bottom: 40px;
			text-align: center;
		}
	}

	.button-upload {
		position: relative;
		display: block;
		margin: 0 auto;
		width: 260px;
		height: 65px;
		line-height: 65px;
		border-radius: 7px;
		background-color: #0f9d58;
		font-size: 17px;
		font-weight: 400;
		text-align: center;
		color: #fff;
		overflow: hidden;

		&:hover {
			background: #0d864b;
		}
	}

	.button-inline {
		width: auto;
		display: inline-block;
		padding-left: 40px;
		padding-right: 40px;
	}

	&-navigation {
		width: 100%;
		display: table;
		font-size: 0;
		text-align: center;
		background: #f3f3f3;
		padding: 38px 0;

		li {
			display: inline-block;
			margin: 0 25px;
		}
		a {
			color: #383838;
			font-size: 20px;
			font-weight: 500;
			cursor: pointer;

			&.active,
			&:hover {
				color: #ff5722;
				text-decoration: none;
			}

			&.active {
				cursor: default;
			}
		}
	}

	.content-toolbar {
		justify-content: flex-start;
	}

	.content-part {
		&-empty {
			strong {
				font-weight: 500;
			}
		}

		.shop-account-recommends {
			&.wrapper {
				padding-bottom: 105px;
			}
			&-empty.wrapper {
				padding-bottom: 226px;
			}
			&-demo {
				position: absolute;
				max-width: 310px;
				top: 50%;
				left: 50%;
				margin-top: 30px;
				text-align: center;
				transform: translate(-50%, -50%);

				p {
					font-size: 16px;
					line-height: 1.3;
					text-align: center;
					color: #7f8c8d;
					margin: 0;
					padding: 0 0 22px;
				}
			}

			&-demo-image {
				position: absolute;
				width: 30%;
				max-width: 373px;
				right: 7%;
				top: 18px;
				@media only screen and (max-width: 1150px) {
					right: 18px;
				}
			}

			.content-show {
				padding-top: 45px;
			}
		}

		.tile-list {
			position: relative;
		}
	}

	.goods-item {
		&:not(.tile-list-item-removed) {
			&.goods-item-all-sold,
			&.goods-item-to-public {
				.goods-image::before {
					opacity: 0.85;
					background-color: #a1887f;
				}
			}

			&.goods-item-change-public {
				.goods-image::before {
					opacity: 1;
					background-color: rgba(0, 140, 204, 0.7);
				}
			}

			&.goods-item-moder {
				.goods-image::before {
					opacity: 1;
					background-color: rgba(255, 163, 3, 0.7);
				}
			}

			&.goods-item-saved-no-public {
				.goods-image::before {
					opacity: 1;
					background-color: rgba(14, 157, 88, 0.7);
				}
			}
		}
	}

	.content-part-no-brands,
	.content-part-no-account {
		h2 {
			font-size: 45px;
			font-weight: 700;
			color: #383838;
			line-height: 45px;
			margin: 0;
			padding-bottom: 31px;
		}
		p {
			font-size: 24px;
			color: #383838;
			line-height: 28px;
			margin: 0;
			padding-bottom: 36px;
		}
	}
	.content-part-knowledges p {
		font-size: 24px;
		color: #383838;
		line-height: 28px;
		margin: 0;
		padding-bottom: 36px;
		@media #{$desktop} {
			font-size: 18px;
			line-height: 24px;
			padding-bottom: 20px;
		}
	}
	.content-part-no-communities {
		position: relative;
		padding-top: 34.5%;
		text-align: center;
		& > div {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 90%;
			transform: translate(-50%, -50%);
		}
		p {
			font-size: 24px;
			color: #383838;
			line-height: 28px;
			margin: 0;
			padding-bottom: 36px;
			@media #{$desktop} {
				font-size: 18px;
				line-height: 24px;
				padding-bottom: 20px;
			}
		}
		.button {
			@media #{$desktop} {
				height: 42px;
				font-size: 15px;
				line-height: 42px;
			}
		}
	}

	.content-part-no-account {
		.wrapper {
			padding: 75px 0;
		}
	}

	.content-part-no-brands {
		padding: 38px 0;
		p {
			text-align: center;
		}
		.icon {
			position: relative;
			top: 3px;
			padding: 5px;
			color: #383838;
			font-size: 25px;
			vertical-align: middle;
		}
	}

	.content-tab-empty .content-part-no-brands {
		padding: 0;
	}

	.content-part-no-news {
		.wrapper {
			padding: 145.5px 0;
		}
		.button {
			width: 220px;
			margin: 10px 23px 0;
			padding: 0;
		}
		.icon {
			position: relative;
			top: 2px;
			padding: 5px;
			color: #383838;
			font-size: 25px;
			vertical-align: middle;
		}
		p + p {
			margin: 1.5em 0;
		}
	}

	.step {
		&-subtitle {
			color: #383838;
			margin: -20px 0 40px;
			font-weight: 400;
		}

		&-title {
			text-align: center;
			font-size: 32px;
			font-weight: 500;
			color: #383838;
			width: 800px;
			margin: 0 auto 50px;
		}
	}

	.no-brands-info {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		padding-top: 5px;
		h4 {
			margin-bottom: 15px;
			font-size: 21px;
			font-weight: 500;
		}
		&-product {
			margin: 25px;
			text-align: center;
		}
	}
}