.brand {
  &-header {
    position: relative;
    background-size: cover;
    background-position: 50% 50%;
    .wrapper {
      padding: 50px;
      height: 500px;
      position: relative;
    }
    &::before {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      height: 50%;
      background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, .25));
      .shop & {
        height: 100%;
        background: rgba(0, 0, 0, .15);
      }
    }
    &._hidden {
      &::before {
        background-color: #a1887f;
        opacity: .85;
      }
      .wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
      }
      h2 {
        font-size: 32px;
        line-height: 38px;
        font-weight: 500;
        color: #fff;
        margin: 0;
        padding-bottom: 40px;
        text-align: center;
      }
      .button {
        height: 65px;
        line-height: 65px;
      }
    }
  }
  &-card {
    position: absolute;
    left: 260px;
    bottom: 50px;
    color: #fff;
    a,
    a:hover {
      color: #fff;
      &.z-index-link {
        position: relative;
        z-index: 4;
      }
    }
  }
  &-btn {
    position: absolute;
    right: 70px;
    bottom: 50px;
  }
  &-image {
    position: absolute;
    bottom: 0;
    left: -210px;
    width: 180px;
    height: 180px;
  }
  &-name {
    font-size: 30px;
    font-weight: 500;
  }
  &-author {
    font-size: 22px;
  }
  &-goods {
    position: absolute;
    right: 50px;
    top: 50px;
    margin: 0;
    padding: 0;
    list-style: none;
    &-item {
      width: 100px;
      height: 100px;
      display: inline-block;
      margin-left: 18px;
      vertical-align: top;
    }
    &-image img {
      transition: all ease .3s;
      .brand-goods-item:hover & {
        transform: scale(1.1);
      }
    }
    &-more {
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      width: 100px;
      height: 100px;
      background: #0f9d58;
      color: #fff;
      font-weight: 400;
      transition: background ease .3s;
      &:hover {
        color: #fff;
        text-decoration: none;
        background: #0d864b;
      }
    }
  }
  &-item {
    overflow: hidden;
    &::before {
      content: '';
      @include posa(0, 0, 0, 0);
      background: rgba(0, 0, 0, .15);
    }
    .brand {
      &-card {
        left: 150px;
        bottom: 30px;
      }
      &-image {
        width: 100px;
        height: 100px;
        left: -120px;
      }
      &-name {
        font-size: 25px;
      }
      &-author {
        font-size: 21px;
        font-weight: 400;
      }
      &-goods {
        right: 20px;
        z-index: 4;
        &-item {
          width: 85px;
          height: 85px;
        }
        &-more {
          width: 85px;
          height: 85px;
        }
      }
    }
    &-link {
      @include posa(0, 0, 0, 0);
      text-indent: -999px;
      z-index: 2;
    }
    &-info {
      background: #ff5722;
      color: #fff;
      padding: 20px 30px;
      position: absolute;
      left: 0;
      right: 0;
      bottom: -210px;
      height: 210px;
      font-size: 16px;
      box-sizing: border-box;
      transition: all .3s;
      z-index: 5;
      @media (max-width: 1150px) {
        bottom: -50%;
        height: 50%;
        padding: 12px 12px;
        font-size: 13px;
      }
      .brand-item:hover & {
        bottom: 0;
      }
      &-name {
        margin: 0 0 10px;
        font-size: 25px;
        font-weight: 500;
        @media (max-width: 1150px) {
          margin: 0 0 6px;
          font-size: 20px;
        }
      }
    }
  }
}

.banner-item {
  .banner-image {
    &::before {
      content: '';
      background: rgba(0, 0, 0, .15);
      @include posa(0, 0, 0, 0);
    }
  }
}
