.item-tools {
	position: absolute;
	right: 10px;
	top: 10px;
	z-index: 10;
	.icon {
		display: inline-block;
		width: 27px;
		height: 27px;
		padding-top: 2px;
		padding-left: 1px;
		color: #fff;
		font-size: 25px;
		line-height: 1;
		letter-spacing: 2px;
		overflow: hidden;
		.no-touch &:hover {
			color: $linkColor;
			text-decoration: none;
		}
	}
	.icon-like-off {
		&.is-favorite:before {
			content: "\75";
		}
	}
	.icon-visible-no {
		&.is-visible:before {
			content: "\25";
		}
	}
	.tile-list-item-removed & {
		display: none;
	}
	&.more-active {
		.tools-collections,
		.tools-dropbox-list {
			visibility: visible;
			opacity: 1;
		}
		.icon-more {
			color: #0f9d58;
		}
	}
}
