.button {
  display: inline-block;
  background: #0f9d58;
  border-radius: 7px;
  line-height: 49px;
  height: 50px;
  padding: 0 2.5em;
  font-size: 17px;
  color: #fff;
  font-weight: 400;
  border: 0;
  cursor: pointer;
  outline: none;
  text-align: center;
  box-sizing: border-box;
  appearance: none;
  white-space: nowrap;
  &:hover {
    color: #fff;
    text-decoration: none;
    background: darken(#0f9d58, 5%);
  }
  &.gray {
    background: #7f8c8d;
    &:hover {
      background: darken(#7f8c8d, 5%);
    }
  }
  &.yellow {
    background: #ffc107;
    &:hover {
      background: darken(#ffc107, 5%);
    }
  }
  &-grey {
    background-color: #7f8c8d;
    &:hover {
      background-color: #6a7475;
    }
  }
  &-red {
    background: #cd0a0a;
    &:hover {
      background-color: #a50000;
    }
  }
  &-scarlet {
    background-color: #ff0000;
    &:hover {
      background-color: darken(#ff0000, 5%);
    }
  }
  &-yellow {
    background: #ffc107;
    &:hover {
      background-color: #edb100;
    }
  }
  .tile-list-item-state &,
  .tile-list-item-type & {
    width: 82%;
    max-width: 238px;
    padding: 0;
    .touch & {
      padding: 0 22px;
    }
  }
}

.plus-button {
  visibility: hidden;
  opacity: 0;
  position: fixed;
  bottom: 70px;
  right: 70px;
  padding: 0;
  width: 56px;
  height: 56px;
  z-index: 5;
  transition: opacity ease .2s;
  &:before,
  &:after {
    content: '';
    position: absolute;
    background: #fff;
  }
  &:before {
    top: 50%;
    left: 27%;
    width: 46%;
    height: 9%;
    margin-top: -4.5%;
  }
  &:after {
    top: 27%;
    left: 50%;
    width: 9%;
    height: 46%;
    margin-left: -4.5%;
  }
  &.active {
    visibility: visible;
    opacity: 1;
  }
  &.float {
    position: absolute;
    bottom: 50px;
  }
}
