.notifications {
  &-item {
    position: relative;
    padding-bottom: 10px;
    &.not-read {
      padding-left: 30px;
      &:before {
        position: absolute;
        display: block;
        top: 0;
        left: 0;
        content: '';
        width: 15px;
        height: 90px;
        background-color: #e91e63; 
      }
    }
  }
  &-content {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 90px;
    padding: 0 73px 0 30px;
    border-radius: 8px;
    background-color: #f3f3f3;
  }
  &-txt {
    padding-right: 35px;
    font-size: 20px;
    font-weight: 500;
    color: #383838;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    line-height: 24px;
    max-height: 48px;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
  &-date {
    color: #7f8c8d;
    font-size: 14px;
    line-height: 1.43;
    white-space: nowrap;
  }
  &-close {
    position: absolute;
    display: block;
    top: 50%;
    right: 30px;
    width: 14px;
    height: 14px;
    color: #bf1b39;
    font-size: 14px;
    margin-top: -12px;
    text-decoration: none;
    &:hover {
      color: #bf1b39;
      text-decoration: none ;
    }
  }
}
