.form {

  &__fieldset {
    display: flex;
    margin-top: 20px;

    @media (max-width: 1169px) {
      flex-direction: column;
    }
  }

  &__field {
    width: 100%;
    height: 48px;
    background: #fff;
    border-radius: 7px;
    border: solid 1px #dcdcdc;

    & + & {
      @media (max-width: 1169px) {
        margin-top: 20px;
      }

      @media (min-width: 1170px) {
        margin-left: 20px;
      }
    }
  }

  &__radio {
    display: flex;
    margin-top: 20px;
    padding: 0;
    list-style: none;

    @media (max-width: 1169px) {
      flex-direction: column;
    }
  }

  &__radio-item {
    position: relative;
    width: 100%;
    background-color: #f3f3f3;
    border-radius: 8px;

    & + & {
      @media (max-width: 1169px) {
        margin-top: 20px;
      }

      @media (min-width: 1170px) {
        margin-left: 20px;
      }
    }
  }

  &__radio-input {
    display: none;
  }

  &__radio-label {
    display: flex;
    align-items: center;
    height: 100%;
    padding: 15px 20px 15px 57px;
    font-weight: 400;
    cursor: pointer;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 20px;
      width: 22px;
      height: 22px;
      margin-top: -11px;
      border: solid 2px #5a5a5a;
      border-radius: 50%;
      cursor: pointer;
      transition: all 250ms;

      .form__radio-input:checked + & {
        border: solid 2px #0f9d58;
        background-color: #0f9d58;
      }
    }
  }
}
