.form {
  margin: 20px 0 0;

  .fieldset,
  &-fieldset {
    border: 0;
    padding: 0;
    p {
      font-size: 16px;
      line-height: 1.38;
      color: #4c4c4c;
      & + .redactor-box {
        margin-top: 25px;
      }
    }
    &.right {
      display: flex;
      justify-content: flex-end;
      .button {
        width: 270px;
        margin-left: 20px;
        padding: 0;
        &:first-child {
          margin-left: 0;
        }
      }
    }
    &.inline {
      display: flex;
      margin-top: 20px;
    }
    &.justify {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      margin-bottom: 50px;
      .button {
        margin-left: 20px;
      }
    }
    &.col {
      width: 47.5%;
    }
    &.tel {
      position: relative;
      display: inline-block;
      margin-top: 20px;
      .tooltip + .tooltip-popup {
        top: 1px;
      }
    }
  }
  &-fieldset-gray {
    position: relative;
    display: flex;
    align-items: center;
    height: 90px;
    padding: 0 20px;
    margin-bottom: 20px;
    border-radius: 8px;
    background-color: #f3f3f3;
  }
  &-label {
    font-size: 24px;
    font-weight: 500;
    &.error {
      color: #cd0a0a;
    }
    &.required:after {
      content: ' *';
    }
    & + .tile-list,
    & + .textarea {
      margin-top: 20px;
    }
  }
  &-field-body {
    display: flex;
    align-items: center;
    max-width: 920px;
    margin-top: 15px;
    > div:not(:last-child) {
      margin-right: 20px;
    }
  }
  .field,
  &-field {
    background-color: #fff;
    border: 1px solid #dcdcdc;
    border-radius: 7px;
    height: 50px;
    box-sizing: border-box;
    position: relative;
    .tel & {
      display: inline-block;
      width: 265px;
      margin-right: 20px;
    }
    & + .form-label {
      margin-top: 30px;
      display: inline-block;
    }
    & + .button {
      margin-top: 20px;
    }
    &.date {
      padding-right: 30px;
      .icon-date {
        position: absolute;
        top: 12px;
        right: 10px;
        font-size: 24px;
        cursor: text;
      }
    }
    &.desc {
      padding-right: 26px;
    }
    &.width-s {
      display: inline-block;
      width: 110px;
      margin-right: 20px;
    }
    &.width-sm {
      min-width: 235px;
    }
    &.width-xm {
      width: 270px;
    }
    &.width-xmm {
      width: 361px;
    }
    &.width-m {
      display: inline-block;
      width: 400px;
      margin-top: 20px;
      margin-right: 16px;
    }
    &.width-mm {
      width: 70%;
    }
    &.width-ml {
      max-width: 525px;
      margin-top: 20px;
    }
    &.width-l {
      max-width: 815px;
      margin-top: 20px;
    }
    &.error {
      border-color: #cd0a0a;
    }
    &.reset::after {
      font-family: 'icons';
      content: "\53";
      color: #cd0a0a;
      position: absolute;
      right: 20px;
      top: 19px;
      font-size: 13px;
    }
    &.good {
      &::after {
        font-family: 'icons';
        content: "\49";
        color: #0f9d58;
        position: absolute;
        right: 20px;
        top: 17px;
        font-size: 17px;
      }
    }
    &-desc {
      position: absolute;
      top: 50%;
      right: 8px;
      transform: translateY(-50%);
    }
    &-tag,
    &-input {
      border: 0;
      padding: 0 15px;
      width: 100%;
      box-sizing: border-box;
      font-size: 18px;
      height: 48px;
      background: none;
      outline-color: #1abc9c;
    }
    &-select {
      box-sizing: border-box;
      position: relative;
      display: inline-block;
      width: 30%;
      height: 48px;
      margin-right: 25px;
      background: #fff;
      border-radius: 7px;
      border: solid 1px #dcdcdc;
      @media only screen and (max-width: 1150px) {
        width: 29%;
      }
      &.select-tags {
        box-sizing: border-box;
        margin-right: 0;
        border: 1px solid #dcdcdc;
        @media only screen and (max-width: 1150px) {
          width: 31%;
        }
      }
      &.width-m {
        width: 340px;
      }
      &.width-l {
        width: 80%;
      }
      &.width-xl {
        width: 90%;
      }
      select {
        width: 100%;
      }
      .select2-container--tags,
      .select2-container--primary {
        width: 100% !important;
      }
    }
    &-radio {
      display: flex;
      padding: 0;
      list-style: none;
      &.size-ml {
        flex-wrap: wrap;
        width: 100%;
      }
      &-item {
        position: relative;
        margin-right: 20px;
        &:last-child {
          margin-right: 0;
        }
        .size-ml &,
        .size-l & {
          width: 50%;
        }
        .size-ml & {
          margin-right: auto;
          margin-bottom: 40px;
          border-right: 20px solid transparent;
          box-sizing: border-box;
        }
        .payment &:not(:first-child) {
          margin-top: 22px;
        }
      }
      &-input {
        display: none;
        &:checked + .form-field-radio-label {
          background: #f3f3f3;
        }
        &:checked + .form-field-radio-label:before {
          border: solid 2px #0f9d58;
          background: #0f9d58;
        }
      }
      &-input#delivery-two {
        &:checked ~ .js-delivery-method {
          display: block;
          background-color: #f95;
        }
      }
      &-label {
        width: 200px;
        height: 50px;
        display: flex;
        align-items: center;
        box-sizing: border-box;
        padding: 15px 15px 15px 57px;
        border-radius: 8px;
        font-weight: 400;
        cursor: pointer;
        &:before {
          content: "";
          position: absolute;
          top: 50%;
          transform: translate(0, -11px);
          left: 20px;
          width: 22px;
          height: 22px;
          box-sizing: border-box;
          border-radius: 50%;
          border: solid 2px #5a5a5a;
          cursor: pointer;
          .size-ml &,
          .size-l & {
            left: 30px;
          }
        }
        .size-ml &,
        .size-l & {
          flex-wrap: wrap;
          width: 100%;
          padding: 15px 15px 15px 82px;
          p {
            margin: 0;
            font-size: 18px;
          }
          span {
            font-size: 15px;
            line-height: 1.47;
            letter-spacing: -0.1px;
            color: #4c4c4c;
          }
        }
        .size-ml & {
          height: 110px;
        }
        .size-l & {
          height: 150px;
        }
        .payment & {
          width: 310px;
        }
      }
      &.payment {
        flex-direction: column;
        margin-top: 20px;
      }
    }
    &.password {
      margin-top: 20px;
      &:first-child {
        margin-top: 0;
      }
      .field-tag {
        width: 100%;
        padding-right: 140px;
        height: 53px;
        line-height: 53px;
      }
      a {
        position: absolute;
        right: 15px;
        font-size: 18px;
        font-weight: 400;
        top: 15px;
      }
      &.error {
        a {
          right: 45px;
        }
      }
    }
  }
  &-www {
    color: #7f8c8d;
    margin-right: 7px;
    opacity: 0.5;
    font-size: 18px;
    line-height: 1.22;
  }
  .field {
    height: 55px;
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  .buttons {
    margin: 20px 0 0;
  }

  &.reset-password {
    .fieldset,
    .button {
      width: 420px;
    }
  }
}

.field-close {
  position: absolute;
  top: 46px;
  right: 10px;
  padding: 10px;
  color: #bf1b39;
  font-size: 13px;
  text-decoration: none;
  transform: translateY(-50%);
  @media (min-width: 1100px) {
    right: 16px;
  }
  @media (min-width: 1150px) {
    right: 20px;
  }
  @media (min-width: 1280px) {
    right: 24px;
  }
  @media (min-width: 1360px) {
    right: 10px;
  }
  &:hover {
    color: #bf1b39;
  }
}

.textarea {
  width: 100%;
  border: 1px solid #dcdcdc;
  border-radius: 7px;

  textarea {
    display: block;
    width: 100%;
    padding: 15px;
    background-color: transparent;
    font-weight: 400;
    outline-color: #1abc9c;
    resize: none;

    &:focus::placeholder {
      opacity: 0;
    }

    .add-collection > & {
      height: 190px;
      @media only screen and (max-width: 1150px) {
        height: 82px;
      }
    }
  }
}

.field-btn {
  display: flex;
  justify-content: space-between;
}

/* -------------------------------- */
.custom-checkbox {height: 24px; display: inline-block; padding-top: 4px;}
[type="checkbox"]:not(:checked),
[type="checkbox"]:checked {
  position: absolute !important;
  left: -9999px;
  margin: 0;
  outline: none;
}
[type="checkbox"]:not(:checked) + label,
[type="checkbox"]:checked + label {
  position: relative;
  padding-left: 18px;
  cursor: pointer;
  font-weight: bold;
  outline: 0;
  height: 24px;
  display: inline-block;
}
.form-inline [type="checkbox"]:not(:checked) + label,
.form-inline [type="checkbox"]:checked + label {
  padding-right: 30px;
}
/* checkbox aspect */
[type="checkbox"]:not(:checked) + label:before,
[type="checkbox"]:checked + label:before {
  display: block;
  width: 22px;
  height: 22px;
  border-radius: 2px;
  border: solid 1px #d5d5d5;
  content: ' ';
  position: absolute;
  top: 0; left: 0;
}
[type="checkbox"]:checked + label:before {
  background: #0f9d58;
  border: solid 1px #0f9d58;
}
/* checked mark aspect */
[type="checkbox"]:not(:checked) + label:after,
[type="checkbox"]:checked + label:after {
  content: '';
  border: 2px solid #fff;
  border-width: 0 0 4px 4px;
  position: absolute;
  width: 10px;
  height: 6px;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  left: 5px;
  top: 5px;
}
/* checked mark aspect changes */
[type="checkbox"]:not(:checked) + label:after {
  opacity: 0;
}
[type="checkbox"]:checked + label:after {
  opacity: 1;
}
/* disabled checkbox */
[type="checkbox"]:disabled:not(:checked) + label:before,
[type="checkbox"]:disabled:checked + label:before {
  box-shadow: none;
  border-color: #bbb;
  background-color: #ddd;
}
[type="checkbox"]:disabled:checked + label:after {
  color: #999;
}
[type="checkbox"]:disabled + label {
  color: #aaa;
}

.features {
  width: 100%;
  margin-top: 40px;
  border-collapse: separate;
  th,
  td {
    position: relative;
    &:not(:last-child) {
      padding-right: 25px;
    }
  }
  th {
    padding: 10px 0;
    color: #4c4c4c;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.38;
    &:not(:last-child) {
      text-align: center;
    }
    &:last-child {
      text-align: left;
    }
    span {
      position: relative;
    }
    .cart & {
      text-align: left;
      &:first-child {
        padding-left: 30px;
      }
    }
  }
  td {
    height: 90px;
    border-bottom: 20px solid transparent;
    &:before {
      position: absolute;
      display: block;
      content: '';
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      height: 90px;
      background-color: #f3f3f3;
      z-index: -1;
    }
    &:not(:first-child):not(:last-child) {
      text-align: center;
      .cart & {
        text-align: left;
      }
    }
    &:last-child {
      text-align: left;
    }
    .cart & {
      font-size: 18px;
    }
  }
  td:nth-child(1) {
    min-width: 140px;
    max-width: 400px;
    padding-left: 20px;
    &:before {
      border-top-left-radius: 7px;
      border-bottom-left-radius: 7px;
    }
    .cart & {
      max-width: 317px;
      min-width: 300px;
      padding-left: 30px;
    }
  }
  td:nth-child(2) {
    max-width: 140px;
  }
  td:nth-child(3) {
    max-width: 170px;
  }
  td:nth-child(4) {
    max-width: 120px;
  }
  td:nth-child(5) {
    max-width: 130px;
    .cart & {
      padding-right: 30px;
      &:before {
        border-top-right-radius: 7px;
        border-bottom-right-radius: 7px;
      }
    }
  }
  td:nth-child(6) {
    min-width: 60px;
    max-width: 160px;
    padding-right: 20px;
    text-align: center;
  }
  td:nth-child(7) {
    min-width: 10px;
    padding-right: 20px;
    &:before {
      border-top-right-radius: 7px;
      border-bottom-right-radius: 7px;
    }
  }

  .form-field-input {
    @media only screen and (max-width: 1150px) {
      padding: 0 13px 0 7px;
      font-size: 16px;
    }
  }
}
form .fieldset .field .error {
  border-color: #cd0a0a;
}
